import React, { useState } from "react";
import { useForm } from "react-hook-form";

import ApexEmptyOptionsChart from "./ApexEmptyOptionsChart";

import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import Col from "react-bootstrap/Col";
import Table from "react-bootstrap/Table";

function makeDataSeries(quotesArray, chart_name) {
  return quotesArray.map((item) => {
    let timestamp = new Date(item.date).getTime();
    return [timestamp, item[chart_name]];
  });
}

function makeDataSeriesForTable(quotesArray, chart_name) {
  return quotesArray.map((item) => {
    return [item.date, item[chart_name]];
  });
}

// let lines = [
//   { name: 'date', label: "Дата", type: "date" },
//   { name: 'chart1', label: "Факт", type: "number" },
//   { name: 'chart2', label: "Стратегия 1", type: "number" },
//   { name: 'chart3', label: "Стратегия 2", type: "number" },
//   { name: 'chart4', label: "Стоп-лосс", type: "number" },
// ]

export default function DatalineQuotesApexChartEditor(props) {
  const { register, handleSubmit, errors } = useForm(); //watch

  const [quotesArray, setQuotesArray] = useState([]);

  const [showChart, setShowChart] = useState(false);

  console.log("ERR", errors);

  function onSubmitForm(formData, e) {
    console.log("The status of formData", formData);

    if (!!formData.date) {
      setQuotesArray((quotesArray) => [...quotesArray, { ...formData }]);
      console.log(quotesArray);

      let series = props.lines.filter(item => item.type !== "date").map(line => {
        return { name: line.label, data: makeDataSeries(quotesArray, line.name) }
      });
      props.setChartSeries(series);
      e.target.reset();
    }
  }

  let series = props.lines.filter(item => item.type !== "date").map(line => {
    return {
      name: line.label, data: makeDataSeriesForTable(quotesArray, line.name)
    }
  })
  console.log(series);

  return (
    <div>
      <Form onSubmit={handleSubmit(onSubmitForm)}>
        <Form.Row>
          {props.lines.map((line, index) =>
            <Form.Group key={line.name} controlId={line.name} as={Col}>
              <Form.Label size="sm">{line.label}</Form.Label>
              <Form.Control
                type={line.type}
                step="0.000001"
                size="sm"
                    {...register(line.name)} 
               
              />
            </Form.Group>
          )}
        </Form.Row>
        <Button variant="secondary" type="submit">Добавить</Button>
      </Form>

      <ShowTable table_data={quotesArray} lines={props.lines}/>

      {quotesArray.length > 1 &&  <Button
        variant="outline-secondary"
        className="m-2"
        onClick={() => setShowChart((showChart) => !showChart)}
      >
        График
      </Button>}
     
      {showChart ?     <ApexEmptyOptionsChart
      showAs={"datalinequotesapexchart"}
        series={series}
        options={{ stroke: { curve: 'smooth' } }}
      /> : null}
    </div>
  );
}

function ShowTable(props) {
  console.log(props)
  return (
    <>
      <Table striped bordered hover>
        <thead>
          <tr>
            {props.lines.map(line => <th key={line.name}>{line.label}</th>)}
          </tr>
        </thead>
        <tbody>
          {props.table_data.map((quote, index) => (
            <tr key={index}>
              {props.lines.map(line =>
                 <td key={line.name+'_'+index}>{quote[line.name]}</td>
                 )}
            </tr>
          ))}
        </tbody>
      </Table>
    </>
  );
}
