import React from "react";
import { useDispatch } from "react-redux";

import Button from "react-bootstrap/Button";
import Collapse from "react-bootstrap/Collapse";

import { load_data, set_spreadsheetTitle, createProtoArray } from "../features/spreadsheet/spreadsheetSlice";

// import ShowXlTableArrayOfArrays from "../mediatemplates/ShowXlTableArrayOfArrays";
import EditableDataTableWithPredefinedIndicators from "../components/EditableDataTableWithPredefinedIndicators";

import SpreadsheetLayout from "../features/spreadsheet/SpreadsheetLayout";

// let formMetaData = {
//     reportDates: ["2018", "2019"],
//     reportIndicators: ["code2110_minfin_ru_66n", "code2120_minfin_ru_66n", "code2100_minfin_ru_66n", "code2210_minfin_ru_66n", "code2220_minfin_ru_66n", "code2200_minfin_ru_66n"]
// }

function doFormMeta(array) {
  //  console.log(array);
  let reportDates = [];
  for (var i = 1; i < array[0].length; i++) {
    reportDates.push(array[0][i]);
  }
  //  console.log(reportDates);
  let reportIndicators = [];
  for (i = 1; i < array.length; i++) {
    reportIndicators.push(array[i][0]);
  }
  //  console.log(reportIndicators);
  return { reportDates, reportIndicators }
}

export default function FormWithPredefinedIndicators({ post }) {
  const dispatch = useDispatch();
  const [open, setOpen] = React.useState(false);

  function OpenAndUpdateSpreadsheet() {
    dispatch(load_data({ protoData: createProtoArray(post.content) }));
    dispatch(set_spreadsheetTitle({ spreadsheetTitle: post.title }));
    setOpen(!open)
  }

 // console.log(post);

  return (
    <div className="m-2">
      <Button
       
        onClick={() => OpenAndUpdateSpreadsheet()}
        aria-controls={"example-collapse-text" + post.id}
        aria-expanded={open}
        variant={open ? "secondary" : "outline-secondary"}
        size="sm"
        block
      >
        {post.title}
      </Button>
      <Collapse in={open}>
        <div id={"example-collapse-text" + post.id}  className="m-2">
          <EditableDataTableWithPredefinedIndicators
            formMetaData={doFormMeta(createProtoArray(post.content, 0, 0))}
            reportIndicatorsDictionary={post.quizString}
            formUrl={post.id} />
            
          <SpreadsheetLayout />
          {/* <BarsGroupedByPeriod /> */}
        </div>
      </Collapse>
    </div>
  );
}