import React from "react";
import firebase from "gatsby-plugin-firebase";

import { SVGBootstrapIconTrash, SVGBootstrapIconTrashFill } from "../svgs";

import Button from "react-bootstrap/Button";

export default function DeleteFireNodeButton(props) {
    const [deleted, setDeleted] = React.useState(null);
    const [error, setError] = React.useState(null);

    function deleteNode() {
        firebase
            .database()
            .ref(props.nodeRef)
            .child(props.id+'/deleted')
            .set(true)
            .then(() => {
                setDeleted(true);
            })
            .catch((error) => {
                setError(error);
            });
    }

    return <Button className="m-1" variant="outline-light"  size="sm"
        onClick={() => deleteNode()}>
        {deleted ? <SVGBootstrapIconTrashFill /> : <SVGBootstrapIconTrash />}
        {error ? <div>{error}</div> : null}
       {" "} Удалить </Button>
}