import React, { useState } from "react";

import Card from 'react-bootstrap/Card'
import InputGroup from 'react-bootstrap/InputGroup'
import FormControl from 'react-bootstrap/FormControl'


function FormulaPriceGordon() {
  const [cF, setCF] = useState(10);
  const [interest, setInterest] = useState(10);
  const [gRate, setGRate] = useState(5);

  let Price = Number(cF) * 100 / (Number(interest) - Number(gRate));

  return <Card bg={"light"} style={{ width: '95%', margin: '1rem' }} >
    <Card.Header>Формула Гордона</Card.Header>
    <Card.Body>
      <Card.Title>Цена = {Price} млн. руб</Card.Title>
      {/* <Card.Text></Card.Text> */}
      <InputGroup size="sm" className="m-1">
        <InputGroup.Prepend>
          <InputGroup.Text id="cf">Ожид. своб. ден. поток перв. года, млн. руб</InputGroup.Text>
        </InputGroup.Prepend>
        <FormControl
          value={cF}
          onChange={e => setCF(e.target.value)}
          type="number"
          aria-label="CF"
          aria-describedby="inputGroup-CF"
        />
      </InputGroup>

      <InputGroup size="sm" className="m-1">
        <InputGroup.Prepend>
          <InputGroup.Text id="interest">Треб. доходность, %</InputGroup.Text>
        </InputGroup.Prepend>
        <FormControl
          value={interest}
          onChange={e => setInterest(e.target.value)}
          type="number"
          aria-label="interest"
          aria-describedby="inputGroup-interest"
        />
      </InputGroup>

      <InputGroup size="sm" className="m-1">
        <InputGroup.Prepend>
          <InputGroup.Text id="gRate">Темп прир. ден. потока, %</InputGroup.Text>
        </InputGroup.Prepend>
        <FormControl
          value={gRate}
          onChange={e => setGRate(e.target.value)}
          type="number"
          aria-label="gRate"
          aria-describedby="inputGroup-gRate"
        />
      </InputGroup>


    </Card.Body>
  </Card>
}

export default FormulaPriceGordon;
