import React from "react";

import ApexEmptyOptionsChart from "../mediatemplates/ApexEmptyOptionsChart.js";

import Container from "react-bootstrap/Container";

function sumOfArrayElements(n) {
    let myarray = [...new Array(n + 1)].map((item, index) => { return 10 * Math.pow(1.02, index) / Math.pow(1.1, index + 1) });
    //  console.log(n);
    return Math.round(myarray.reduce((a, b) => a + b, 0) * 100) / 100
}

export default function GordonBars(props) {

    let series = [{
        name: 'Кэш фло',
        data: [...new Array(40)].map((item, index) => { return Math.round(10 * Math.pow(1.02, index) * 100 / Math.pow(1.1, 0)) / 100 })
    }, {
        name: 'Дисконтированный к моменту покупки кэш фло',
        data: [...new Array(40)].map((item, index) => { return Math.round(10 * Math.pow(1.02, index) * 100 / Math.pow(1.1, index + 1)) / 100 })
    }, {
        name: 'Накопленный дисконтированный кэш-фло',
        data: [...new Array(40)].map((item, index) => { return sumOfArrayElements(index) })
    }];

    let categories = [...new Array(40)].map((item, index) => { var d = new Date(); return d.setFullYear(d.getFullYear() + 1 + index) })
   

    return <Container className="mt-3" >
        <ApexEmptyOptionsChart
      showAs={"datalinebarapexchart"}
            series={series}
            categories={categories}
        />
    </Container>
}


