import React from "react";
import { useSelector } from "react-redux";

import firebase from "gatsby-plugin-firebase";
import { useForm } from "react-hook-form";
import ReactHtmlParser from "react-html-parser";

import { selectApplication } from "../features/application/applicationSlice";
import { chooseDictionary, selectUserDataItems, findValueForIndicatorPeriod } from "../features/data/dataSlice";

import DropDownInputGroup from "./DropDownInputGroup";

import ApexEmptyOptionsChart from "../mediatemplates/ApexEmptyOptionsChart";

import { ProcessBalanceStackedBars } from "../mediatemplates/PrepareDataFromDictionaryForChart"; // MediaTemplatesAccountingWithProfitsCash";

import Container from "react-bootstrap/Container";
import Table from "react-bootstrap/Table";
import Alert from "react-bootstrap/Alert";
import Tab from "react-bootstrap/Tab";
import Tabs from "react-bootstrap/Tabs";
import InputGroup from "react-bootstrap/InputGroup";
import FormControl from "react-bootstrap/FormControl";
import Button from "react-bootstrap/Button";
import ButtonGroup from "react-bootstrap/ButtonGroup";


function generateArrayOfYears() {
  let current = new Date().getFullYear();
  let max = current + 10;
  let years = [];

  for (var i = current - 10; i <= max; i++) {
    years.push(i);
  }
  return years;
}

export default function TemplateChartEditor() {
  const applicationState = useSelector(selectApplication);
  const userDataItems = useSelector(selectUserDataItems);
  const [key, setKey] = React.useState('source');

  const [rows, setRows] = React.useState([]);
  const [columns, setColumns] = React.useState([]);
  const [chartType, setChartType] = React.useState("balancestackedbarswithpredefinedindicators");

  const [editPostTitleComment, doEditPostTitleComment] = React.useState(true);
  const [savedSuccessfully, DoSavedSuccessfully] = React.useState(false);

  function setFormObject(objectWithNamesValues) {
    if (applicationState.email.length > 6) {
      let userEmail = applicationState.email.replace(/[^a-zA-Z0-9]/g, "_");
      let idPost = firebase.database().ref(userEmail).child("posts").push().key;

      let postObject = {
        id: idPost,
        title:
          objectWithNamesValues.title.length > 2
            ? objectWithNamesValues.title
            : "Диаграмма",
        theme: applicationState.currentProjectTitle, //currentProjectMediaAndDataAndTemplatesURL,
        answer: applicationState.currentProjectMediaAndDataAndTemplatesURL,
        comment: objectWithNamesValues.comment, //Тема
        type: chartType,
        content: { requiredseries: rows, requiredcategories: columns },
        quizString: applicationState.currentProjectReportIndicatorsDictionary,
        deleted: false,
        email: applicationState.email,
        user: applicationState.user,
        avatarUrl: !!applicationState?.avatarUrl
          ? applicationState.avatarUrl
          : null,
        date: new Intl.DateTimeFormat("ru", {
          weekday: "short",
          year: "numeric",
          month: "short",
          day: "numeric",
          hour: "numeric",
          minute: "numeric",
        }).format(new Date()), //Date().toJSON()
      };

      console.log(postObject);
      //    dispatch(createPost(postObject));
      var updates = {};
      updates["/usersCraft/" + userEmail + "/posts/" + idPost] = postObject;
      updates["/usersTemplates/charts/" + idPost] = {
        ...postObject,
        deleted: true,
      };

      return firebase
        .database()
        .ref()
        .update(updates)
        .then(() => {
          DoSavedSuccessfully(true);
          doEditPostTitleComment(false);
          setTimeout(window.location.reload(), 3000); //; props.handleClose()
        });
    }
  }


  function addNewRow(newRow) {
    setRows((rows) => [...rows, newRow]);
  }

  function addNewColumn(newCol) {
    setColumns((columns) => [...columns, newCol]);
  }

  let reportIndicatorsDictionaryArray = !!applicationState.currentProjectReportIndicatorsDictionary
    ? chooseDictionary(applicationState.currentProjectReportIndicatorsDictionary).map((item) => item.ru_title)
    : ["Выберите стандарт"];

  let projectMediaAndDataAndTemplatesURL =
    applicationState.projectMediaAndDataAndTemplatesURL;

  function getDataForIndicatorPeriod(row, columns) {
    return columns.map(column => findValueForIndicatorPeriod(
      userDataItems,
      row,
      column,
      //    projectMediaAndDataAndTemplatesURL
    ))
  }

  let processedSeries = rows.map(indicator => {
    return { name: indicator, data: getDataForIndicatorPeriod(indicator, columns) }
  })

 // console.log(processedSeries);


  return (
    <Container>
      <Alert variant="light">
        <div> {applicationState.currentProjectTitle}</div>
        <div>Стандарт отчетности: {applicationState.currentProjectReportIndicatorsDictionary}</div>
        <div>{ReactHtmlParser(applicationState.currentProjectComment)}</div>
        <div>{applicationState.currentProjectSourseDataURL}</div>
      </Alert>
      <hr />

      <Tabs
        id="controlled-tab-example"
        activeKey={key}
        onSelect={(k) => setKey(k)}
      >
        <Tab eventKey="source" title="Данные">
          <Container className="mt-3">
            <DropDownInputGroup
              items={reportIndicatorsDictionaryArray}
              title={"Новая строка"}
              buttonTitle={"+"}
              setSelectedItem={addNewRow}
            />

            <DropDownInputGroup
              items={generateArrayOfYears()}
              title={"Новая дата (период)"}
              buttonTitle={"+"}
              setSelectedItem={addNewColumn}
            />

            {(!!rows && rows.length > 0) || (!!columns && columns.length > 0) ? (
              <Table striped bordered hover size="sm">
                <thead>
                  <tr>
                    <th>
                      <span className="ml-1">Показатель</span>
                    </th>
                    {columns.map((item) => (
                      <th key={item}>{item}</th>
                    ))}
                  </tr>
                </thead>
                <tbody>
                  {rows.map((row) => (
                    <tr key={row}>
                      <td>
                        <span className="ml-1">{row}</span>
                      </td>
                      {columns.map((column) => (
                        <td key={column}>
                          {findValueForIndicatorPeriod(
                            userDataItems,
                            row,
                            column,
                            projectMediaAndDataAndTemplatesURL
                          )}
                        </td>
                      ))}
                    </tr>
                  ))}
                </tbody>
              </Table>
            ) : null}
          </Container>
        </Tab>

        <Tab eventKey="type" title="Тип">
          <ButtonGroup size="sm" className="m-3">
            <Button variant="outline-secondary" onClick={() => setChartType("balancestackedbarswithpredefinedindicators")}>Баланс Bars Stacked</Button>
            <Button variant="outline-secondary" onClick={() => setChartType("arraycategoriesbarapexchartwithpredefinedindicators")}  >Bars</Button>
            <Button variant="outline-secondary" onClick={() => setChartType("arraycategorieslineapexchartwithpredefinedindicators")} >Line</Button>
          </ButtonGroup>
          <Container className="mt-3">
            {!!rows && rows.length > 0 && !!columns && columns.length > 0
              && chartType === "balancestackedbarswithpredefinedindicators" &&
              <ProcessBalanceStackedBars
                categories={columns}
                reportIndicators={rows}
                processedSeries={processedSeries}
              />
              // <ApexChartsEditor
              //   categories={columns}
              //   reportIndicators={rows}
              // />
            }
            {!!rows && rows.length > 0 && !!columns && columns.length > 0
              && (chartType === "arraycategoriesbarapexchartwithpredefinedindicators" || chartType === "arraycategorieslineapexchartwithpredefinedindicators") &&
              <ApexEmptyOptionsChart
                series={processedSeries}
                categories={columns}
                showAs={chartType.replace('withpredefinedindicators','')}
              />
            }
          </Container>
        </Tab>

      </Tabs>

      {editPostTitleComment &&
        !!rows &&
        rows.length > 0 &&
        !!columns &&
        columns.length > 0 ? (
        <PostFormHook title={""} setFormObject={setFormObject} />
      ) : null}

      {savedSuccessfully ? <Alert variant={"success"}>Успешно!</Alert> : null}
    </Container>
  );
}

let modalForm = [
  { name: "title", title: "Название", type: "text" },
  { name: "comment", title: "Комментарий", type: "textarea" },
];

function PostFormHook(props) {
  const { register, handleSubmit, reset } = useForm({
    //watch, errors
    defaultValues: { title: props.title },
  });

  React.useEffect(() => {
    reset({ title: props.title });
  }, [props.title]);

  return (
    <form onSubmit={handleSubmit(props.setFormObject)}>
      {modalForm.map((item) => (
        <InputGroup className="mb-3" size="sm" key={item.name}>
          <InputGroup.Prepend>
            <InputGroup.Text style={{ width: "8rem" }}>
              {item.title}
            </InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            key={item.name}
       //     name={item.name}
            type={item.type}
            {...register(item.name)}
           
            as={item.type === "textarea" ? "textarea" : "input"}
          />
        </InputGroup>
      ))}
      <Button variant="outline-secondary" type="submit" size="sm">
        Сохранить
      </Button>
    </form>
  );
}

