import React from "react";
import "./dots.css";

const Dots = ({ dots = ["100", "110", "121"] }) => (
  <div className="dotscontainer">
        {dots.map((item, index) => (
          <div key={index} className="node">
            <div className="status"></div>
            {item}
          </div>
        ))}
        </div>
);

export default Dots;
