import React from "react";

const MarkdownEditorLazy = React.lazy(() => import("./MarkdownEditorLazy.js"))

export default (props) => {
    const isSSR = typeof window === "undefined"
    return <>
      {!isSSR && (
        <React.Suspense fallback={<div />}><MarkdownEditorLazy {...props} /></React.Suspense>
      )}
    </>
}
