function makeChartCandle(quoteItem) {
  let datestring = quoteItem.date;
  let year = datestring.substring(0, 4);
  let month = datestring.substring(4, 6);
  let day = datestring.substring(6, 8);
  let date = +new Date(year, month - 1, day);
  return {
    x: date,
    y: [+quoteItem.open, +quoteItem.high, +quoteItem.low, +quoteItem.close],
  };
}

function makeChartCandleFromCSV(quoteItem) {
  let datestring = quoteItem.quotedate;
  let year = datestring.substring(0, 4);
  let month = datestring.substring(4, 6);
  let day = datestring.substring(6, 8);
  let date = +new Date(year, month - 1, day);
  return {
    x: date,
    y: [+quoteItem.open, +quoteItem.high, +quoteItem.low, +quoteItem.close],
  };
}

function makeVolumeFromCSV(quoteItem) {
  let datestring = quoteItem.quotedate;
  let year = datestring.substring(0, 4);
  let month = datestring.substring(4, 6);
  let day = datestring.substring(6, 8);
  let date = +new Date(year, month - 1, day);
  return {
    x: date,
    y: [+quoteItem.vol],
  };
}

function makeVolume(quoteItem) {
  let datestring = quoteItem.date;
  let year = datestring.substring(0, 4);
  let month = datestring.substring(4, 6);
  let day = datestring.substring(6, 8);
  let date = +new Date(year, month - 1, day);
  return {
    x: date,
    y: [+quoteItem.vol],
  };
}

