function ImportedDataLayout() {
  const quotesArray = useSelector(selectTempData).dataArray;

  let chartData = quotesArray.map((quoteItem) => {
    return makeChartCandleFromCSV(quoteItem);
  });
  let seriesDataLinear = quotesArray.map((quoteItem) => {
    return makeVolumeFromCSV(quoteItem);
  });

  let candles = quotesArray.map((item) => {
    return {
      open: +item.open,
      high: +item.high,
      close: +item.close,
      low: +item.low,
      date: item.quotedate,
    };
  });

  if (!quotesArray || quotesArray.length === 0) {
    return <DefaultTechAnLayout />;
  }

  return (
    <div className="mt-3">
      <ApexCandlestickCombo
        seriesData={chartData}
        seriesDataLinear={seriesDataLinear}
      />
      <FindPatterns candles={candles} quotesArray={quotesArray} />
    </div>
  );
}

