function FindPatterns({ candles, quotesArray }) {
  const [pattern, setPattern] = React.useState(null);
  const [cases, setCases] = React.useState(null);

  let found = [];

  var i;
  for (i = 0; i < candles.length - 3; i++) {
    const threecandles = [candles[i], candles[i + 1], candles[i + 2]];
    // console.log(detector(threecandles));
    let somethingFound = detector(threecandles);

    const iterable = [-4, -3, -2, -1, 0, 1, 2, 3, 4];
    let situationCandles = [];
    for (let value of iterable) {
      if (i + value > -1 && i + value < candles.length) {
        situationCandles.push(makeChartCandle(quotesArray[i + value]));
      }
    }

    if (somethingFound.bullish.length > 0) {
      found.push({
        name: somethingFound.bullish[0],
        type: "bullish",
        chartData: situationCandles,
        //  [
        //   makeChartCandle(quotesArray[i]),
        //   makeChartCandle(quotesArray[i + 1]),
        //   makeChartCandle(quotesArray[i + 2]),
        //   makeChartCandle(quotesArray[i + 3]),
        // ],
      });
    }
    if (somethingFound.bearish.length > 0) {
      found.push({
        name: somethingFound.bearish[0],
        type: "bearish",
        chartData: situationCandles,
        // [
        //   makeChartCandle(quotesArray[i]),
        //   makeChartCandle(quotesArray[i + 1]),
        //   makeChartCandle(quotesArray[i + 2]),
        //   makeChartCandle(quotesArray[i + 3]),
        // ],
      });
    }
  }

  // console.log(found);

  const uniqueTypes = !!found
    ? [...new Set(found.map((item) => item.name))]
    : []; // [ 'A', 'B']
  console.log(uniqueTypes);

  let ru_patterns = uniqueTypes.map((unique_item) => {
    let found_ru = dictionary_tech_analysis_patterns.find(
      (dict_item) => dict_item.code === unique_item
    )?.ru_title;
    // console.log(found_ru)
    if (!!found_ru) return found_ru;
    return unique_item;
  });

  function setPatternCоde(ru_title) {
    // console.log(ru_title);
    let selected_pattern = dictionary_tech_analysis_patterns.find(
      (item) => item.ru_title === ru_title
    );
    let show_pattern_with_code = selected_pattern.code;
    // console.log(show_pattern_with_code);
    let chartsByType = found.filter(
      (item) => item.name === show_pattern_with_code
    );
    // console.log(chartsByType);
    setPattern(selected_pattern);
    setCases(chartsByType);
  }

  return (
    <div>
      <DropDownInputGroup
        items={ru_patterns}
        title={"Найденные свечные паттерны"}
        buttonTitle={"Выбрать"}
        setSelectedItem={setPatternCоde}
      />
      {!!pattern && !!cases && (
        <ShowPatternCases cases={cases} pattern={pattern} />
      )}
    </div>
  );
}

function ShowPatternCases({ cases, pattern }) {
  console.log(cases, pattern);

  return (
    <Card bg={"light"} style={{ width: "95%", margin: "1rem" }}>
      <Card.Header>Вероятные примеры паттерна</Card.Header>
      <Card.Body>
        <Card.Title>{pattern.ru_title}</Card.Title>
        <Media>
          <img
            width={256}
            height={256}
            className="mr-3"
            src={pattern.image}
            alt="Generic placeholder"
            style={{ filter: "grayscale(100%)" }} //, objectFit: "cover"
          />
          <Media.Body>
            <h5>{pattern.ru_title}</h5>
            <div>{ReactHtmlParser(pattern.ru_comment)}</div>
          </Media.Body>
        </Media>
        <hr />
        {!!cases && <PossibleCases cases={cases} />}
      </Card.Body>
    </Card>
  );
}

