import React from "react";

const HTMLEditorBallonBlockLazy = React.lazy(() => import("./HTMLEditorBallonBlockLazy.js"))

export default (props) => {
    const isSSR = typeof window === "undefined"
    return <>
      {!isSSR && (
        <React.Suspense fallback={<div />}><HTMLEditorBallonBlockLazy {...props} /></React.Suspense>
      )}
    </>
}
