import React, { useEffect, useState } from "react";



import Card from 'react-bootstrap/Card';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import FormControl from 'react-bootstrap/FormControl';
import InputGroup from 'react-bootstrap/InputGroup';
import Button from 'react-bootstrap/Button';

import Alert from 'react-bootstrap/Alert';

import { SVGSmileNeutral, SVGBootstrapIconEmojiAngry, SVGBootstrapIconEmojiSmile } from "../svgs";

function calcAverage(arr) {
    return arr.reduce((p, c) => p + c, 0) / arr.length
}

function calcStDev(arr) {
    // let parser = new FormulaParser();
    // return parser.parse('STDEV.S(1,2,3,4,5,6)');
    return 10
}


export default function CaseFinancialLeverage() {
    const [yearProfit, setYearProfit] = useState(20);
    const [profits, setProfits] = useState([]);

    const [equityReturns, setEquityReturns] = useState([]);

    const [debt, setDebt] = useState(0);
    const [costOfDebt, setCostOfDebt] = useState(10);

    const [bankruptcy, setBankruptcy] = useState(false);


    useEffect(() => {
        const interval = setInterval(() => {
            let profit = (Math.floor(Math.random() * 50 * 10) - 10) / 10;
            setYearProfit(profit);
            setProfits(profits => [...profits, profit]);

            let equityProfit = profit - parseFloat(debt) * parseFloat(costOfDebt) / 100;
            let equityProfitability = equityProfit * 100 / (100 - parseFloat(debt));
            if (equityProfitability <= -100) { setBankruptcy(true) };
            setEquityReturns(equityReturns => [...equityReturns, equityProfitability]);
        }, 2000);
        return () => clearInterval(interval);
    }, [debt, costOfDebt]);


    let equity = 100 - parseFloat(debt);
    let equityProfit = yearProfit - parseFloat(debt) * parseFloat(costOfDebt) / 100;
    let equityProfitability = equityProfit * 100 / equity;

    return <Card bg={"light"} style={{ width: '95%', margin: '1rem' }}>
        <Card.Header>Симулятор</Card.Header>
        <Card.Body>
            <Card.Title>Финансовый рычаг и риски</Card.Title>

            <InputGroup size="sm" className="mb-1">
                <InputGroup.Prepend>
                    <InputGroup.Text id="inputGroup-sizing-sm">Долг</InputGroup.Text>
                </InputGroup.Prepend><FormControl
                    aria-label="Small" aria-describedby="inputGroup-sizing-sm"
                    type="number"
                    value={debt}
                    onChange={e => setDebt(e.target.value)} />
            </InputGroup>

            <InputGroup size="sm" className="mb-1">
                <InputGroup.Prepend>
                    <InputGroup.Text id="inputGroup-sizing-sm">Стоимость долга</InputGroup.Text>
                </InputGroup.Prepend><FormControl
                    aria-label="Small" aria-describedby="inputGroup-sizing-sm"
                    type="number"
                    onChange={e => setCostOfDebt(e.target.value)} />
            </InputGroup>
            <Container>
                <Row>
                    <Col>Активы</Col>
                    <Col>Пассивы</Col>
                    <Col>Инвесторы</Col>
                    <Col>Рент</Col>
                    <Col>Оценка</Col>
                </Row>
                <Row>
                    <Col>100</Col>
                    <Col>СК {equity}</Col>
                    <Col>{Math.floor(equityProfit * 10) / 10}</Col>
                    <Col>{Math.floor((equityProfit) * 100 * 10 / equity) / 10}</Col>
                    <Col>
                        {equityProfitability < parseFloat(costOfDebt) && <SVGBootstrapIconEmojiAngry width="30" height="30" />}
                        {equityProfitability > parseFloat(costOfDebt) && <SVGBootstrapIconEmojiSmile width="30" height="30" />}
                    </Col>
                </Row>
                <Row>
                    <Col>-</Col>
                    <Col>Долг {debt}</Col>
                    <Col>{debt * costOfDebt / 100}</Col>
                    <Col>{costOfDebt}</Col>
                    <Col><SVGSmileNeutral width="30" height="30" /></Col>
                </Row>
            </Container>
            <Card.Text>
                Прибыль {Math.floor(yearProfit * 10) / 10}
            </Card.Text>

            {bankruptcy && <Alert variant="danger">Банкрот</Alert>}


            <Button className="m-1" variant="outline-secondary" size="sm"
                onClick={() => { setProfits(profits => []); setEquityReturns(equityReturns => []); setBankruptcy(false) }}
            >Считать заново</Button>

            <Row>
                <Col> <Card.Text>
                    Средняя рентабельность активов {Math.floor(calcAverage(profits) * 10) / 10}
                </Card.Text>
                <Card.Text> Ср квадр отклонение {calcStDev(profits)} </Card.Text>
                

                </Col>
                <Col>
                    <Card.Text>
                        Средняя рентабельность собственного капитала {Math.floor(calcAverage(equityReturns) * 10) / 10}
                    </Card.Text>
                </Col>
            </Row>



        </Card.Body>
    </Card>
}

