function transliterate(word) {
  return word
    .split("")
    .map(function (char) {
      return a[char] || char;
    })
    .join("");
}

function doFormMeta(array) {
  //  console.log(array);
  let reportDates = [];
  for (var i = 1; i < array[0].length; i++) {
    reportDates.push(array[0][i]);
  }
  //  console.log(reportDates);
  let reportIndicators = [];
  for (i = 1; i < array.length; i++) {
    reportIndicators.push(array[i][0]);
  }
  //  console.log(reportIndicators);
  return { reportDates, reportIndicators }
}


export function EditableDataTableWithPredefinedIndicatorsForQuiz(
  {
    id = "calculations/-M__wWrqImWnKWKFA3Uy",
    title,
    theme
  }) {
  const { data: calculation, loading, error } = useFirebaseNode(
    "usersTemplates/" + id
  );

  if (!!loading || !calculation) {
    return <div>...</div>;
  }
  if (!!error) {
    return <div>:-(</div>;
  }

 // console.log(calculation);

  return <CaseEditableDataTableWithPredefinedIndicators
    // <EditableDataTableWithPredefinedIndicators
    formMetaData={doFormMeta(createProtoArray(calculation.content, 0, 0))}
    // reportIndicatorsDictionary={calculation.quizString}
    //   formUrl={calculation.id}
    //  caseProjectSourseDataURL={projectSourseDataURL}
    //  currentProjectReportIndicatorsDictionary={currentProjectReportIndicatorsDictionary}
    // projectMoneyScale={projectMoneyScale}
    calculationContent={calculation.content}
    title={title}
    theme={theme}
  />

  // <div>Editable Data Table With Predefined Indicators</div>
}

function CaseEditableDataTableWithPredefinedIndicators({
  formMetaData,
  // reportIndicatorsDictionary,
  // formUrl,
  //  caseProjectSourseDataURL,
  // currentProjectReportIndicatorsDictionary,
  // projectMoneyScale,
  calculationContent,
  title,
  theme
}) {
  const userData = useSelector(selectUserData);
  const applicationState = useSelector(selectApplication);
  const content = useSelector(selectSpreadsheetProtoData);
  const calculatedSpreadsheet = useSelector(selectSpreadsheetData);
  const dispatch = useDispatch();
  const { register, handleSubmit, watch, reset } = useForm({
    defaultValues: doFindValuesForFormByProjectMediaAndDataAndTemplatesURL(
      userData.data,
      formMetaData,
      applicationState.currentProjectMediaAndDataAndTemplatesURL, // caseProjectSourseDataURL.replace(/[^a-zA-Z0-9]/g, "_")
      applicationState.currentProjectReportIndicatorsDictionary), // currentProjectReportIndicatorsDictionary
  });

  let user = applicationState.user;
  let email = applicationState.email;
  let userEmail = email.replace(/[^a-zA-Z0-9]/g, "_");

  let projectUrl = applicationState.currentProjectMediaAndDataAndTemplatesURL;
  let projectMoneyScale = applicationState.currentProjectMoneyScale;
  let caseProjectSourseDataURL = applicationState.currentProjectSourseDataURL;
  let currentProjectReportIndicatorsDictionary = applicationState.currentProjectReportIndicatorsDictionary;

  React.useEffect(() => {
  //  console.log(calculationContent);
    dispatch(load_data({ protoData: createProtoArray(calculationContent) }));
    dispatch(set_spreadsheetTitle({ spreadsheetTitle: title }));
  }, []);

  // console.log(
  //   formMetaData,
  //   reportIndicatorsDictionary,
  //   formUrl,
  //   caseProjectSourseDataURL,
  //   currentProjectReportIndicatorsDictionary
  // );


  function onSubmit(data) {
    console.log("onSubmit");
    console.log(data);

    let date = new Intl.DateTimeFormat("ru", {
      weekday: "short",
      year: "numeric",
      month: "short",
      day: "numeric",
      hour: "numeric",
      minute: "numeric",
    }).format(new Date());

    let currentDay = new Intl.DateTimeFormat("en", {
      weekday: "short",
      year: "numeric",
      month: "short",
      day: "numeric",
    })
      .format(new Date())
      .replace(/[^a-zA-Z0-9]/g, "_");

    let updates = {};

    formMetaData.reportIndicators.map((indicator) => {
      formMetaData.reportDates.map((period) => {
        let item = indicator + "___" + period;
        let found = userData.data.find(
          (dataItem) =>
            dataItem.form_name === item
            && dataItem.projectMediaAndDataAndTemplatesURL === projectUrl
        );

        console.log(found);

        let foundInDictionary = chooseDictionary(currentProjectReportIndicatorsDictionary).
          filter((dictItem) => dictItem.ru_title === indicator);
        let codeForFirebase = transliterate(foundInDictionary[0].ru_title).replace(
          /[^a-zA-Z0-9]/g,
          "_"
        );

        if (!found) {

          let idUserDataItem = firebase.database().ref("fundamentals/byUserEmail").child(userEmail).push().key;

          let newDataItem = {
            id: idUserDataItem,
            projectTitle: title,
            projectComment: theme,
            projectMediaAndDataAndTemplatesURL: projectUrl,
            projectSourseDataURL: caseProjectSourseDataURL,
            projectMoneyScale: projectMoneyScale,
            form_name: item,
            code: indicator,
            reportIndicatorsDictionary: currentProjectReportIndicatorsDictionary,
            ru_title: foundInDictionary[0].ru_title,
            ru_comment: foundInDictionary[0].ru_comment,
            ru_source: foundInDictionary[0].ru_source,
            period: period,
            value: data[item],
            date: date,
            user: user,
            email: email,
          };

          //    console.log(newDataItem);
          dispatch(createDataItem(newDataItem));

          updates["openmediadata/" + projectUrl + "/" + idUserDataItem] = newDataItem;

          updates["fundamentals/byProjectDataPublicationUrl/" + projectUrl + "/" + codeForFirebase + "/"
            + idUserDataItem] = newDataItem;

          updates["fundamentals/byReportCodesOrIndicators/" + codeForFirebase + "/"
            + idUserDataItem
          ] = newDataItem;

          updates["fundamentals/byUserEmail/" + userEmail + "/"
            + idUserDataItem] = newDataItem;

          //    console.log(updates);
        } else {
          if (found.value !== data[item]) {
            //                   console.log('Надо изменить значение ' + item);
            updates["openmediadata/" + projectUrl + "/" + found.id + "/value"] = data[item];
            updates["openmediadata/" + projectUrl + "/" + found.id + "/date"] = date;

            updates["fundamentals/byProjectDataPublicationUrl/" + projectUrl + "/" + codeForFirebase + "/" +
              found.id + "/value"] = data[item];

            updates["fundamentals/byProjectDataPublicationUrl/" + projectUrl + "/" + codeForFirebase + "/" +
              found.id + "/date"] = date;

            updates["fundamentals/byReportCodesOrIndicators/" + codeForFirebase + "/" +
              found.id + "/value"] = data[item];

            updates["fundamentals/byReportCodesOrIndicators/" + codeForFirebase + "/" +
              found.id + "/date"] = date;

            updates["fundamentals/byUserEmail/" + userEmail + "/" + found.id + "/value"] = data[item];

            updates["fundamentals/byUserEmail/" + userEmail + "/" +
              found.id + "/date"] = date;

            dispatch(editDataItem({ ...found, value: data[item], date: date }));
          }
        }
      });
    })


    updates[
      "/currentLaboratoryDay/" +
      currentDay +
      "/projects/" +
      projectUrl +
      "/" +
      userEmail
    ] = "+";

    console.log(updates);

    return firebase
      .database()
      .ref()
      .update(updates)
      .then(() => {
        console.log("Data saved");
      })
      .catch((error) => console.log(error));
  }

  function doReset() {
    //      console.log('doTransferToSpreadsheet');
    reset({
      ...doFindValuesForFormByProjectMediaAndDataAndTemplatesURL(
        userData.data,
        formMetaData,
        caseProjectSourseDataURL.replace(/[^a-zA-Z0-9]/g, "_"),
        currentProjectReportIndicatorsDictionary
      ),
    });
  }


  function doTransferToSpreadsheet() {
    //      console.log('doTransferToSpreadsheet');
  //  console.log(watch());
  //  console.log(content);
    const newContent = produce(content, (draft) => {
      for (var i = 1; i < content.length; i++) {
        var row = content[i];
        for (var j = 1; j < row.length; j++) {
          let objectKey = content[i][0] + "___" + content[0][j];
          draft[i][j] = watch(objectKey) || ""; // content[i][j]
        }
      }
    });
  //  console.log(newContent)
    dispatch(load_data({ protoData: newContent }));
  }

  function doTransferToForm() {
    //      console.log('doTransferToForm');
    let newFormValues = {};
    for (var i = 0; i < content.length; i++) {
      var row = content[i];
      for (var j = 0; j < row.length; j++) {
        if (
          content[i][0].toString().length > 2 &&
          content[0][j].toString().length > 2
        ) {
          let objectKey = content[i][0] + "___" + content[0][j];
          newFormValues[objectKey] = calculatedSpreadsheet[i][j]; //content[i][j]
        }
      }
    }
    //      console.log(newFormValues);
    reset({ ...newFormValues });
  }



  return <div>
    <form onSubmit={handleSubmit(onSubmit)}>
      <InputGroup className="mb-3" size="sm">
        <InputGroup.Prepend>
          <InputGroup.Text style={{ width: "15rem" }}>
            Показатели
        </InputGroup.Text>
        </InputGroup.Prepend>
        {formMetaData.reportDates.map((item) => (
          <FormControl key={item} placeholder={item} readOnly />
        ))}
      </InputGroup>

      {formMetaData.reportIndicators.map((row) => (
        <InputGroup className="mb-3" size="sm" key={row}>
          <InputGroup.Prepend>
            <InputGroup.Text style={{ width: "15rem" }}>
              {row}
            </InputGroup.Text>
          </InputGroup.Prepend>
          {formMetaData.reportDates.map((period) => (
            <FormControl
              key={row + "___" + period}
            //  name={row + "___" + period}
              type="number"
              step="0.1"
              {...register(row + "___" + period)}             
              required
            />
          ))}
        </InputGroup>
      ))}

      {/* {projectUrl && projectUrl.length > 6 
        //  applicationState?.currentProjectMediaAndDataAndTemplatesURL &&
        // applicationState?.currentProjectMediaAndDataAndTemplatesURL.length > 6
       ? ( */}
      <div>
        <Button
          className="m-1"
          variant="outline-secondary"
          type="submit"
          size="sm"
        >
          Сохранить
        </Button>
        <Button
          className="m-1"
          variant="outline-secondary"
          size="sm"
          onClick={() => doReset()}
        >
          Восстановить
        </Button>
        <Button
          className="m-1"
          variant="outline-secondary"
          size="sm"
          onClick={() => doTransferToSpreadsheet()}
        >
          Передать в Расчет
        </Button>
        <Button
          className="m-1"
          variant="outline-secondary"
          size="sm"
          onClick={() => doTransferToForm()}
        >
          Передать из Расчета
        </Button>
      </div>
      {/* ) : null} */}
    </form>
  </div>

}



export default function EditableDataTableWithPredefinedIndicators(props) {
  const userData = useSelector(selectUserData);
  const applicationState = useSelector(selectApplication);
  const content = useSelector(selectSpreadsheetProtoData);
  const calculatedSpreadsheet = useSelector(selectSpreadsheetData);
  const dispatch = useDispatch();
  const { register, handleSubmit, watch, reset } = useForm({
    defaultValues: doFindValuesForForm(
      userData.data,
      props.formMetaData,
      applicationState.currentProjectTitle,
      applicationState.currentProjectReportIndicatorsDictionary
    ),
  });

  let user = applicationState.user;
  let email = applicationState.email;
  let userEmail = email.replace(/[^a-zA-Z0-9]/g, "_");




  let projectSourseDataURL = !!applicationState?.currentProjectSourseDataURL
    ? applicationState.currentProjectSourseDataURL.replace(/[^a-zA-Z0-9]/g, "_")
    : transliterate(projectSourseDataURL.currentProjectTitle).replace(
      /[^a-zA-Z0-9]/g,
      "_"
    );
  //  console.log(projectUrl);

  let projectUrl = applicationState.currentProjectMediaAndDataAndTemplatesURL;

  function onSubmit(data) {
    let date = new Intl.DateTimeFormat("ru", {
      weekday: "short",
      year: "numeric",
      month: "short",
      day: "numeric",
      hour: "numeric",
      minute: "numeric",
    }).format(new Date());

    let currentDay = new Intl.DateTimeFormat("en", {
      weekday: "short",
      year: "numeric",
      month: "short",
      day: "numeric",
    })
      .format(new Date())
      .replace(/[^a-zA-Z0-9]/g, "_");

    let updates = {};

    props.formMetaData.reportIndicators.map((indicator) => {
      props.formMetaData.reportDates.map((period) => {
        let item = indicator + "___" + period;
        let found = userData.data.find(
          (dataItem) =>
            dataItem.form_name === item &&
            dataItem.projectMediaAndDataAndTemplatesURL === projectUrl
        );
        //         console.log(found);
        let foundInDictionary = chooseDictionary(applicationState.currentProjectReportIndicatorsDictionary)
        .filter((dictItem) => dictItem.ru_title === indicator);

        let codeForFirebase =
          // !!foundInDictionary[0]?.code ? foundInDictionary[0]?.code :
          transliterate(foundInDictionary[0].ru_title).replace(
            /[^a-zA-Z0-9]/g,
            "_"
          );

        if (!found) {
          //       let userEmail = applicationState.email.replace(/[^a-zA-Z0-9]/g, "_");
          let idUserDataItem = firebase
            .database()
            .ref("openmediadata")
            .child(projectUrl)
            .push().key;

          let newDataItem = {
            id: idUserDataItem,
            projectTitle: applicationState.currentProjectTitle,
            projectComment: applicationState.currentProjectComment,
            projectMediaAndDataAndTemplatesURL: projectUrl,
            projectSourseDataURL: applicationState.currentProjectSourseDataURL,
            projectMoneyScale: applicationState.currentProjectMoneyScale,
            form_name: item,
            code: indicator,
            reportIndicatorsDictionary: applicationState.currentProjectReportIndicatorsDictionary,
            ru_title: foundInDictionary[0].ru_title,
            ru_comment: foundInDictionary[0].ru_comment,
            ru_source: foundInDictionary[0].ru_source,
            period: period,
            value: data[item],
            date: date,
            user: user,
            email: email,
          };

          updates[
            "openmediadata/" + projectUrl + "/" + idUserDataItem
          ] = newDataItem;

          updates[
            "fundamentals/byProjectDataPublicationUrl/" +
            projectSourseDataURL +
            "/" +
            codeForFirebase +
            "/" +
            idUserDataItem
          ] = newDataItem;

          updates[
            "fundamentals/byReportCodesOrIndicators/" +
            codeForFirebase +
            "/" +
            idUserDataItem
          ] = newDataItem;

          updates[
            "fundamentals/byUserEmail/" +
            userEmail + "/" +
            idUserDataItem
          ] = newDataItem;

          dispatch(createDataItem(newDataItem));
        } else {
          if (found.value !== data[item]) {
            //                   console.log('Надо изменить значение ' + item);
            updates["openmediadata/" + projectUrl + "/" + found.id + "/value"] =
              data[item];
            updates[
              "openmediadata/" + projectUrl + "/" + found.id + "/date"
            ] = date;

            //       found.id + "/value" ] = data[item];
            // found.id + "/date" ] = date;

            updates[
              "fundamentals/byProjectDataPublicationUrl/" +
              projectSourseDataURL +
              "/" +
              codeForFirebase +
              "/" +
              found.id +
              "/value"
            ] = data[item];

            updates[
              "fundamentals/byProjectDataPublicationUrl/" +
              projectSourseDataURL +
              "/" +
              codeForFirebase +
              "/" +
              found.id +
              "/date"
            ] = date;

            updates[
              "fundamentals/byReportCodesOrIndicators/" +
              codeForFirebase +
              "/" +
              found.id +
              "/value"
            ] = data[item];

            updates[
              "fundamentals/byReportCodesOrIndicators/" +
              codeForFirebase +
              "/" +
              found.id +
              "/date"
            ] = date;

            updates[
              "fundamentals/byUserEmail/" +
              userEmail +
              "/" +
              found.id +
              "/date"
            ] = date;

            updates[
              "fundamentals/byUserEmail/" +
              userEmail +
              "/" +
              found.id +
              "/value"
            ] = data[item];

            dispatch(editDataItem({ ...found, value: data[item], date: date }));
          }
        }
        return null;
      });
      return null;
    });

    updates[
      "/currentLaboratoryDay/" +
      currentDay +
      "/projects/" +
      projectUrl +
      "/" +
      props.formUrl
    ] = "+";

    console.log(updates);

    return firebase
      .database()
      .ref()
      .update(updates)
      .then(() => {
        console.log("Data saved");
      })
      .catch((error) => console.log(error));
  }

  function doReset() {
    //      console.log('doTransferToSpreadsheet');
    reset({
      ...doFindValuesForForm(
        userData.data,
        props.formMetaData,
        applicationState.currentProjectTitle,
        applicationState.currentProjectReportIndicatorsDictionary
      ),
    });
  }

  function doTransferToSpreadsheet() {
    //      console.log('doTransferToSpreadsheet');
    //      console.log(watch());
    //      console.log(content);
    const newContent = produce(content, (draft) => {
      for (var i = 1; i < content.length; i++) {
        var row = content[i];
        for (var j = 1; j < row.length; j++) {
          let objectKey = content[i][0] + "___" + content[0][j];
          draft[i][j] = watch(objectKey) || ""; // content[i][j]
        }
      }
    });
    // console.log(newContent)
    dispatch(load_data({ protoData: newContent }));
  }

  function doTransferToForm() {
    //      console.log('doTransferToForm');
    let newFormValues = {};
    for (var i = 0; i < content.length; i++) {
      var row = content[i];
      for (var j = 0; j < row.length; j++) {
        if (
          content[i][0].toString().length > 2 &&
          content[0][j].toString().length > 2
        ) {
          let objectKey = content[i][0] + "___" + content[0][j];
          newFormValues[objectKey] = calculatedSpreadsheet[i][j]; //content[i][j]
        }
      }
    }
    //      console.log(newFormValues);
    reset({ ...newFormValues });
  }

  // console.log(props);
  // console.log(applicationState);

  return (
    <div>
      <form onSubmit={handleSubmit(onSubmit)}>
        <InputGroup className="mb-3" size="sm">
          <InputGroup.Prepend>
            <InputGroup.Text style={{ width: "15rem" }}>
              Показатели
            </InputGroup.Text>
          </InputGroup.Prepend>
          {props.formMetaData.reportDates.map((item) => (
            <FormControl key={item} placeholder={item} readOnly />
          ))}
        </InputGroup>

        {props.formMetaData.reportIndicators.map((row) => (
          <InputGroup className="mb-3" size="sm" key={row}>
            <InputGroup.Prepend>
              <InputGroup.Text style={{ width: "15rem" }}>
                {row}
              </InputGroup.Text>
            </InputGroup.Prepend>
            {props.formMetaData.reportDates.map((period) => (
              <FormControl
                key={row + "___" + period}
          //      name={row + "___" + period}
                type="number"
                step="0.1"
                {...register(row + "___" + period)}                      
                required
              />
            ))}
          </InputGroup>
        ))}

        {projectUrl && projectUrl.length > 6
          //  applicationState?.currentProjectMediaAndDataAndTemplatesURL &&
          // applicationState?.currentProjectMediaAndDataAndTemplatesURL.length > 6
          ? (
            <div>
              <Button
                className="m-1"
                variant="outline-secondary"
                type="submit"
                size="sm"
              >
                Сохранить
            </Button>
              <Button
                className="m-1"
                variant="outline-secondary"
                size="sm"
                onClick={() => doReset()}
              >
                Восстановить
            </Button>
              <Button
                className="m-1"
                variant="outline-secondary"
                size="sm"
                onClick={() => doTransferToSpreadsheet()}
              >
                Передать в Расчет
            </Button>
              <Button
                className="m-1"
                variant="outline-secondary"
                size="sm"
                onClick={() => doTransferToForm()}
              >
                Передать из Расчета
            </Button>
              {/* <Button className="m-1" variant="outline-secondary" size="sm" onClick={() => doOpenModalWithChart()}>Диаграмма</Button> */}
            </div>
          ) : null}
      </form>
    </div>
  );
}
