import React from "react";
import { useSelector } from "react-redux";

import { selectTempData } from "../features/temp/tempSlice"; //, emptyTempData

import ApexCandlestickCombo from "../mediatemplates/ApexCandlestickCombo";

// https://www.npmjs.com/package/currency-pattern-detector
// https://www.npmjs.com/package/ta.js
//https://github.com/Bitvested/ta.js/blob/main/index.js

function calculateRSIArray(data, len) {
  var length = !len ? 13 : len - 1;
  var pl = [],
    arrsi = [];
  for (var i = 1; i < data.length; i++) {
    var diff = ((data[i] - data[i - 1]) / data[i]) * 100;
    pl.push(diff);
    if (pl.length >= length) {
      var gain = 0,
        loss = 0;
      for (let q in pl) {
        if (pl[q] < 0) loss += pl[q];
        if (pl[q] >= 0) gain += pl[q];
      }
      gain /= length;
      loss = Math.abs(loss) / length;
      let rsi = Number(100 - 100 / (1 + gain / loss));
      arrsi.push(rsi);
      diff = ((data[i] - data[i - 1]) / data[i]) * 100;
      pl.push(diff);
      pl.splice(0, 1);
    }
  }
  return arrsi;
}

function makeChartCandleFromCSV(quoteItem) {
  let datestring = quoteItem.quotedate;
  let year = datestring.substring(0, 4);
  let month = datestring.substring(4, 6);
  let day = datestring.substring(6, 8);
  let date = +new Date(year, month - 1, day);
  return {
    x: date,
    y: [+quoteItem.open, +quoteItem.high, +quoteItem.low, +quoteItem.close],
  };
}

function makeRSI(quotesArray, len) {
  let closeArray = [];
  for (var i = 0; i < quotesArray.length; i++) {
    console.log(quotesArray[i].close);
    closeArray.push(quotesArray[i].close);
  }
  let rsiValues = calculateRSIArray(closeArray, len);

  let rsiArray = [];
  for (i = 0; i < quotesArray.length; i++) {
    let datestring = quotesArray[i].date;
    let year = datestring.substring(0, 4);
    let month = datestring.substring(4, 6);
    let day = datestring.substring(6, 8);
    let date = +new Date(year, month - 1, day);
    if (i < len) {
      rsiArray.push({
        x: date,
        y: null,
      });
    } else {
      rsiArray.push({
        x: date,
        y: rsiValues[i],
      });
    }
  }
  console.log(rsiArray);
  return rsiArray;
}

export default function TechnicalAnalysisRSI() {
  return (
    <div>
      <ImportedDataLayout />
    </div>
  );
}

function ImportedDataLayout() {
  const quotesArray = useSelector(selectTempData).dataArray;

  let chartData = quotesArray.map((quoteItem) => {
    return makeChartCandleFromCSV(quoteItem);
  });
  
  let seriesDataRSI = makeRSI(quotesArray, 3);
  console.log(seriesDataRSI);

  if (!quotesArray || quotesArray.length === 0) {
    return <div>Загрузите данные</div>;
  }

  return (
    <div className="mt-3">
      <ApexCandlestickCombo
        seriesData={chartData}
        seriesDataLinear={seriesDataRSI}
      />
    </div>
  );
}
