import React, { useState } from "react";
import { useSelector, useDispatch } from "react-redux";

import firebase from "gatsby-plugin-firebase";

import { selectApplication } from "../features/application/applicationSlice";
import { getDataItems, getDataSuccess, getDataFailure } from "../features/data/dataSlice";

import { useFirebaseNode } from "../hooks/useFirebaseNode";

import FormWithPredefinedIndicators from "./FormWithPredefinedIndicators";
import { PrepareReduxDataFromDictionaryForChart } from "../mediatemplates/PrepareDataFromDictionaryForChart";
import { GetSeriesCategoriesOptionsForApexChart, GetReadyScatterApexChart, GetReadyDatalineQuotesApexChart } from "../mediatemplates/ShowMedia";

import CreateOrShowScatterApexChartPostForProject from "../mediatemplates/CreateOrShowScatterApexChartPostForProject";
import CreateOrShowCommentPostForProject from "../mediatemplates/CreateOrShowCommentPostForProject";



export default function ProjectFormsWithPredefinedIndicators() {
  const dispatch = useDispatch();

  const [status, setStatus] = useState({
    loadingData: false,
    dataArray: undefined,
    errorData: undefined
  });

  const email = useSelector(selectApplication).email;
  // const currentProjectTitle = useSelector(selectApplication).currentProjectTitle;
  const currentProjectMediaAndDataAndTemplatesURL = useSelector(
    selectApplication
  ).currentProjectMediaAndDataAndTemplatesURL;

  let userEmail = email.replace(/[^a-zA-Z0-9]/g, "_");

  const { data: userPosts, loading, error } = useFirebaseNode(
    "usersCraft/" + userEmail + "/posts/"
  );

  function fetchNow(url) {
    dispatch(getDataItems());
    setStatus({ loadingData: true });
    firebase
      .database()
      .ref('openmediadata/' + url)
      .once("value")
      .then(snapshot => {
        let dataObject = snapshot.val();
        let dataArray = !!dataObject ? Object.keys(dataObject).map((item) => dataObject[item]) : [];
        dispatch(getDataSuccess(dataArray));
        setStatus({ loadingData: false, dataArray });
      })
      .catch((errorData) => {
        dispatch(getDataFailure());
        setStatus({ loadingData: false, errorData });
      });
  }

  React.useEffect(() => {
    //  console.log(currentProjectMediaAndDataAndTemplatesURL);
    if (currentProjectMediaAndDataAndTemplatesURL) {
      fetchNow(currentProjectMediaAndDataAndTemplatesURL);
    }
  }, [currentProjectMediaAndDataAndTemplatesURL]);



  if (!!loading || !userPosts || !!status.loadingData || !status.dataArray) { return <div>...</div> }

  if (!!error) { return <div>:-(</div> }


  let projectContents = Object.keys(userPosts)
    .map((item) => userPosts[item])
    .filter(
      (item) =>
        item.answer === currentProjectMediaAndDataAndTemplatesURL && item.deleted !== true
    );


 // console.log(projectContents);


  return (
    <div>
      {projectContents.map((item) => {
        if (item.type === "calculation") { return <FormWithPredefinedIndicators key={item.id} post={item} /> }

        if (item.type === "balancestackedbarswithpredefinedindicators" ||
          item.type === "arraycategoriesbarapexchartwithpredefinedindicators" ||
          item.type === "arraycategorieslineapexchartwithpredefinedindicators") {

          return <PrepareReduxDataFromDictionaryForChart chart={item} key={item.id} />
        }

        if (item.type === "html" || item.type === "markdown") { return <CreateOrShowCommentPostForProject key={item.id} firenode={item} /> }

        if (item.type === "datetimebarapexchart" || item.type === "datetimelineapexchart" ||
          item.type === "arraycategorieslineapexchart" || item.type === "arraycategoriesbarapexchart") {
          return <GetSeriesCategoriesOptionsForApexChart key={item.id} firenode={item.content} mediaItem={item.type} />
        }


        if (item.type === "scatterquotesapexchart" || item.type === "scatterquotesapexchartoptions" ||
          item.type === "scatterapexchart") {
          return <CreateOrShowScatterApexChartPostForProject key={item.id} firenode={item} /> // <GetReadyScatterApexChart key={item.id} firenode={item.content} />
        }

        if (item.type === "datalinequotesapexchart" || item.type === "datalinequotesapexchartoptions" ||
          item.type === "datetimeareanocategoriesapexchart") {
          return <GetReadyDatalineQuotesApexChart key={item.id} firenode={item.content} />
        }



        console.log(item);
        return <div>Неизвестное содержимое</div>

      })}
    </div>
  );
}
