let modalForm = [
  { name: "currentProjectTitle", title: "Название проекта" },
  { name: "currentProjectComment", title: "Комментарий" },
  {
    name: "currentProjectSourseDataURL",
    title: "URL Интернет источник отчетности",
  },
  {
    name: "currentProjectMoneyScale",
    title: "Денежная единица (млн. руб, млрд долл и т.п.)",
  },
];

function NewProjectForm(props) {
  const email = useSelector(selectApplication).email;
  const user = useSelector(selectApplication).user;
  const avatarUrl = useSelector(selectApplication)?.avatarUrl;

  const { register, handleSubmit, watch } = useForm(); //, errors
  const [
    reportIndicatorsDictionary,
    setReportIndicatorsDictionary,
  ] = React.useState(null);
  const dispatch = useDispatch();

  function onSubmit(data) {
    //   console.log(data);

    if (email.length > 6) {
      let userEmail = email.replace(/[^a-zA-Z0-9]/g, "_");
      let idPost = firebase.database().ref(userEmail).child("posts").push().key;

      let postObject = {
        id: idPost,
        title: data?.currentProjectComment?.comment || "Комментарий",
        theme:
          data.currentProjectTitle.length > 2
            ? data.currentProjectTitle
            : "Проект",
        answer: data.currentProjectSourseDataURL,
        comment:
          data.currentProjectComment.comment > 2
            ? data.currentProjectComment.comment
            : "Комментарий",
        type: "project",
        content: reportIndicatorsDictionary,
        quizString: data.currentProjectMoneyScale,
        deleted: false,
        email: email,
        user: user,
        avatarUrl: !!avatarUrl ? avatarUrl : null,
        date: new Intl.DateTimeFormat("ru", {
          weekday: "short",
          year: "numeric",
          month: "short",
          day: "numeric",
          hour: "numeric",
          minute: "numeric",
        }).format(new Date()), //Date().toJSON()
      };

      //     console.log(postObject);
      //    dispatch(createPost(postObject));
      var updates = {};
      updates["/usersCraft/" + userEmail + "/posts/" + idPost] = postObject;
      updates["/openmedia/" + idPost] = postObject;
      updates["/usersTemplates/projects/" + idPost] = postObject;

      return firebase
        .database()
        .ref()
        .update(updates)
        .then(() => {
          //      DoSavedSuccessfully(true);
          //      doEditPostTitleComment(false);
          dispatch(
            set_currentProject({
              currentProjectTitle:
                data.currentProjectTitle.length > 2
                  ? data.currentProjectTitle
                  : "Проект",
              currentProjectComment:
                data.currentProjectComment.comment > 2
                  ? data.currentProjectComment.comment
                  : "Комментарий",
              currentProjectMediaAndDataAndTemplatesURL: idPost,
              currentProjectSourseDataURL: data.currentProjectSourseDataURL,
              currentProjectMoneyScale: data.currentProjectMoneyScale,
              currentProjectReportIndicatorsDictionary: reportIndicatorsDictionary,
            })
          );
          setTimeout(window.location.reload(), 3000); //; props.handleClose()
        });
    }
  }

  return (
    <form onSubmit={handleSubmit(onSubmit)}>
      <DropDownInputGroup
        items={["Project Expert", "РСБУ Приказ Минфин 66н 2010 г."]}
        title={"Стандарт отчетности"}
        buttonTitle={"Выбрать"}
        setSelectedItem={setReportIndicatorsDictionary}
      />
      {!!reportIndicatorsDictionary &&
        modalForm.map((item) => (
          <InputGroup className="mb-3" size="sm" key={item.name}>
            <InputGroup.Prepend>
              <InputGroup.Text style={{ width: "20rem" }}>
                {item.title}
              </InputGroup.Text>
            </InputGroup.Prepend>
            <FormControl
              key={item.name}
           //   name={item.name}
              type="text"
              {...register(item.name)}
             
            />
          </InputGroup>
        ))}
      <Button
        variant="outline-secondary"
        type="submit"
        size="sm"
        disabled={Object.keys(watch()).length < 3}
      >
        Сохранить
      </Button>
    </form>
  );
}

function DrowpdownWithUserProjects() {
  const applicationState = useSelector(selectApplication);
  const email = applicationState.email;
  let userEmail = email.replace(/[^a-zA-Z0-9]/g, "_");
  const dispatch = useDispatch();

  const { data: userPosts, loading, error } = useFirebaseNode(
    "usersCraft/" + userEmail + "/posts/"
  );

  if (!!loading || !userPosts) {
    return <div>...</div>;
  }
  if (!!error) {
    return <div>:-(</div>;
  }
  let data = Object.keys(userPosts)
    .map((item) => userPosts[item])
    .filter(
      (item) =>
        item.type === "project" &&
        item.title !== applicationState?.currentProjectTitle
    );

  //  console.log(data);

  return (
    <>
      {data.length === 0 ? (
        <span>Создайте проект</span>
      ) : (
        <SplitButton
          size="sm"
          drop="right"
          variant="outline-secondary"
          title={
            !!applicationState?.currentProjectTitle
              ? applicationState?.currentProjectTitle
              : data[data.length - 1]?.theme
          }
        >
          {" "}
          {data.map((item, index) => (
            <Dropdown.Item
              key={item.id}
              eventKey={index}
              onClick={() => {
                dispatch(
                  set_currentProject({
                    currentProjectTitle: item.theme, //title,
                    currentProjectComment: item.comment,
                    currentProjectMediaAndDataAndTemplatesURL: item.id,
                    currentProjectSourseDataURL: item.answer, //content,
                    currentProjectMoneyScale: item.quizString,
                    currentProjectReportIndicatorsDictionary: item.content,
                  })
                );
              }}
            >
              {item.theme}
            </Dropdown.Item>
          ))}
        </SplitButton>
      )}
    </>
  );
}

function NewFormFromTemplate() {
  const currentProjectReportIndicatorsDictionary = useSelector(
    selectApplication
  ).currentProjectReportIndicatorsDictionary;
  const { data: calculations, loading, error } = useFirebaseNode(
    "usersTemplates/calculations"
  );
  const [rows, setRows] = useState(null);

  if (!!loading || !calculations) {
    return <div>...</div>;
  }
  if (!!error) {
    return <div>:-(</div>;
  }

  let dataList = Object.keys(calculations)
    .map((item) => calculations[item])
    .filter(
      (item) => item.quizString === currentProjectReportIndicatorsDictionary && !item.deleted
    );

  function doSelectTemplate(content) {
    console.log(content);
    let foundRows = [];
    Object.keys(content).map((key, index) => {
      let reqKey = !!content?.["A" + index.toString()]
        ? content["A" + index.toString()]
        : null;
      console.log(reqKey);
      if (reqKey !== null) {
        foundRows.push(content["A" + index.toString()]);
      }
    });
    setRows(foundRows);
    console.log(foundRows);
  }

  return (
    <div>
      <SplitButton
        size="sm"
        drop="right"
        variant="outline-secondary"
        title="Доступные Шаблоны"
        className="m-2"
      >
        {" "}
        {dataList.map((item, index) => (
          <Dropdown.Item
            key={item.id}
            eventKey={index}
            onClick={() => doSelectTemplate(item.content)}
          >
            {item.title}
          </Dropdown.Item>
        ))}
      </SplitButton>
      {!!rows && <TemplateTableEditor rows={rows} />}
    </div>
  );
}
