import React, { useState } from "react";

import Card from 'react-bootstrap/Card'
import InputGroup from 'react-bootstrap/InputGroup'
import FormControl from 'react-bootstrap/FormControl'


function FormulaInterestGordon() {
  const [cF, setCF] = useState(10);
  const [price, setPrice] = useState(125);
  const [gRate, setGRate] = useState(5);

  let interest = (Number(cF) *100 / Number(price)) + Number(gRate);

  return <Card bg={"light"} style={{ width: '95%', margin: '1rem' }} >
    <Card.Header>Формула Гордона</Card.Header>
    <Card.Body>
      <Card.Title>Требуемая инвесторами доходность = {interest} %</Card.Title>
      {/* <Card.Text></Card.Text> */}

      <InputGroup size="sm" className="m-1">
        <InputGroup.Prepend>
          <InputGroup.Text id="price">Факт. цена акции, млн руб</InputGroup.Text>
        </InputGroup.Prepend>
        <FormControl
          value={price}
          onChange={e => setPrice(e.target.value)}
          type="number"
          aria-label="price"
          aria-describedby="inputGroup-price"
        />
      </InputGroup>


      <InputGroup size="sm" className="m-1">
        <InputGroup.Prepend>
          <InputGroup.Text id="cf">Ожид. ден. поток перв. года, млн. руб</InputGroup.Text>
        </InputGroup.Prepend>
        <FormControl
          value={cF}
          onChange={e => setCF(e.target.value)}
          type="number"
          aria-label="CF"
          aria-describedby="inputGroup-CF"
        />
      </InputGroup>

     

      <InputGroup size="sm" className="m-1">
        <InputGroup.Prepend>
          <InputGroup.Text id="gRate">Темп прироста денежного потока, %</InputGroup.Text>
        </InputGroup.Prepend>
        <FormControl
          value={gRate}
          onChange={e => setGRate(e.target.value)}
          type="number"
          aria-label="gRate"
          aria-describedby="inputGroup-gRate"
        />
      </InputGroup>


    </Card.Body>
  </Card>
}

export default FormulaInterestGordon;
