export default function CaseCardsWithCompanyReports(props) {
  const [showCases, setShowCases] = useState(false);
  const dispatch = useDispatch();

  function loadCases() {
    dispatch(
      set_currentProject({
        currentProjectComment: "Выберите проект (финансовую отчетность) или добавьте свой вариант с указанием URL адреса pdf(zip, 7z...) файла",
     }));
    setShowCases(true)
  }

  if (!showCases) {
    return <Navbar className="bg-light justify-content-between mt-3 mb-3">
      <Navbar.Brand >{props.setTitle}</Navbar.Brand>
      <Button variant="outline-secondary" size="sm" style={{width: "6rem"}}
        onClick={() => loadCases(true)}
      >Решать</Button>
    </Navbar>
  }
  return <ShowCaseCardsWithCompanyReports {...props} />
}

function ShowCaseCardsWithCompanyReports({ set, setTitle }) {
  const dispatch = useDispatch();
  const email = useSelector(selectApplication).email;
  const [projectsWithReports, setProjectsWithReports] = useState([]);

  React.useEffect(() => {

    let userEmail = email.replace(/[^a-zA-Z0-9]/g, "_");

    dispatch(getDataItems());
    // setStatus({ loadingData: true });
    firebase
      .database()
      .ref('fundamentals/byUserEmail/' + userEmail)
      .once("value")
      .then(snapshot => {
        let dataObject = snapshot.val();
        let dataArray = !!dataObject ? Object.keys(dataObject).map((item) => dataObject[item]) : [];
        dispatch(getDataSuccess(dataArray));
        //   setStatus({ loadingData: false, dataArray });
        //   console.log(dataArray);

      })
      .catch((errorData) => {
        dispatch(getDataFailure());
        //  setStatus({ loadingData: false, errorData });
      });


    firebase
      .database()
      .ref("usersCraft/" + userEmail + "/posts/")
      .once("value")
      .then(snapshot => {
        let dataObject = snapshot.val();
        //   console.log(qSets[set][0]);
        let data = Object.keys(dataObject)
          .map((item) => dataObject[item])
          .filter((item) => item.type === "project");
        //      console.log(data);
        let userProjects = data.map(project => {
          return {
            id: project.id,
            url: project.answer,
            htmlText: "<a href='" + project.answer + "' target='_blank'>" + project.theme + " " + project.quizString + "</a>",
            currentProjectReportIndicatorsDictionary: project.content,
            comment: project.comment,
            title: project.theme

          }
        });
        setProjectsWithReports([...userProjects, ...companiesData]);
      })
      .catch((errorData) => {
        console.log(errorData);
        setProjectsWithReports([...companiesData]);

      });

  }, []);

  if (projectsWithReports.length > 0) {
    return <CaseCardsWithCompanyReportsWithUserProject
      set={set}
      setTitle={setTitle}
      projectsWithReports={projectsWithReports}
    />
  }
  return <Navbar className="bg-light justify-content-between mt-3 mb-3">
    <Navbar.Brand >{setTitle}</Navbar.Brand>
    <Button variant="outline-secondary" size="sm" style={{width: "6rem"}}>...</Button>
  </Navbar>
}


export function CaseCardsWithCompanyReportsWithUserProject({ set, setTitle, projectsWithReports }) {
  const applicationState = useSelector(selectApplication);
  const dispatch = useDispatch();

  const [selectedQuiz, setSelectedQuiz] = useState(0);
  const [projectSourseDataURL, setSelectedProjectSourseDataURL] = useState("projectSourseDataURL");
  const [loading, setLoading] = useState(false);

  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);




  function selectCompanyReport(id) {
    setLoading(true);
    setSelectedProjectSourseDataURL(projectsWithReports[id]?.url);
    dispatch(
      set_currentProject({
        currentProjectTitle: projectsWithReports[id]?.title,
        currentProjectComment: projectsWithReports[id]?.htmlText,
        currentProjectMediaAndDataAndTemplatesURL: projectsWithReports[id]?.url.replace(/[^a-zA-Z0-9]/g, "_"),
        currentProjectSourseDataURL: projectsWithReports[id]?.url,
        currentProjectMoneyScale: "млрд. руб",
        currentProjectReportIndicatorsDictionary: projectsWithReports[id]?.currentProjectReportIndicatorsDictionary,
      }));
    setLoading(false);
  }

  function doSelectQuiz(index) {
    setLoading(true);
    setSelectedQuiz(index);
    setLoading(false);
  }

  let quizprops = qSets[set][selectedQuiz];

  return <div><Navbar bg="light"><Navbar.Brand >{setTitle}</Navbar.Brand>
    <NavDropdown title="Выберите" id="basic-nav-dropdown">
      <NavDropdown.Item>Отчетность</NavDropdown.Item>
      <NavDropdown.Divider />
      {projectsWithReports.map((item, index) => <NavDropdown.Item key={index} onClick={() => selectCompanyReport(index)}>{item.title}</NavDropdown.Item>)}
    </NavDropdown>
    <Button
      onClick={() => handleShow()}
      variant="outline-secondary"
      size="sm"
      data-toggle="tooltip"
      data-placement="bottom"
      title="Создать новый проект"
      className="justify-content-end"
    >
      + Свой отчет
          </Button>
  </Navbar>
    <br />



    <ButtonGroup size={qSets[set].length < 5 ? "lg" : "sm"}>
      {qSets[set].map((quiz, index) => (
        <Button
          variant="outline-secondary"
          onClick={() => doSelectQuiz(index)}
          key={index}
        >
          {qSets[set].length < 10 ? <span className="m-2" >{index + 1}</span> : <small>{index + 1}</small>}
        </Button>
      ))}
    </ButtonGroup>
    {loading ? <div>...</div> : <QuizCardWithCompanyReports key={selectedQuiz + projectSourseDataURL}
      setId={selectedQuiz + 1} {...quizprops} />}
    <Modal show={show} onHide={handleClose} size="lg">
      <Modal.Header closeButton>
        <Modal.Title>Сведения о проекте</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <NewProjectForm handleClose={handleClose} />
      </Modal.Body>
      {/*    <Modal.Footer>
        <Button variant="secondary" onClick={handleClose}>Закрыть</Button> 
      </Modal.Footer> */}
    </Modal>
  </div>
}


export function QuizCardWithCompanyReports(props) {
  const content = useSelector(selectSpreadsheetProtoData);

  const currentProjectMediaAndDataAndTemplatesURL = useSelector(selectApplication).currentProjectMediaAndDataAndTemplatesURL;
  const currentProjectSourseDataURL = useSelector(selectApplication).currentProjectSourseDataURL;
  const currentProjectTitle = useSelector(selectApplication).currentProjectTitle;
  const currentProjectComment = useSelector(selectApplication).currentProjectComment;

  const email = useSelector(selectApplication).email;
  const user = useSelector(selectApplication).user;
  const avatarUrl = useSelector(selectApplication)?.avatarUrl;
  const [savedAnswer, setSavedAnswer] = useState(null);

  const [value, setValue] = useState("");

  function handleCheckAnswer() {
    // setShowAnswer(true);
    // if (
    //   parseFloat(value) / parseFloat(answer) < 1.02 &&
    //   parseFloat(value) / parseFloat(answer) > 0.98
    // ) {
    //   setAnswerIsRight(true);
    if (email.length > 6) {
      let userEmail = email.replace(/[^a-zA-Z0-9]/g, "_");
      let idPost = firebase.database().ref(userEmail).child("posts").push()
        .key;
      let currentDay = new Intl.DateTimeFormat("en", {
        weekday: "short",
        year: "numeric",
        month: "short",
        day: "numeric",
      })
        .format(new Date())
        .replace(/[^a-zA-Z0-9]/g, "_");

      let postObject = {
        id: idPost, //Math.floor(Math.random() * 1001),
        title: props.text,
        theme: currentProjectTitle,
        answer: value,
        comment: currentProjectMediaAndDataAndTemplatesURL, //Тема
        type: "spreadsheet",
        content: createProtoObject(content),
        quizString: props.text + '. Рассчитано по отчетности ' + currentProjectTitle + ' по адресу ' + currentProjectSourseDataURL,
        deleted: false,
        email: email,
        user: user,
        avatarUrl: !!avatarUrl ? avatarUrl : null,
        date: new Intl.DateTimeFormat("ru", {
          weekday: "short",
          year: "numeric",
          month: "short",
          day: "numeric",
          hour: "numeric",
          minute: "numeric",
        }).format(new Date()), //Date().toJSON()
      };

      let currentDayObject = {
        id: idPost,
        title: props.text,
        theme: props.theme,
        email: email,
        user: user,
        avatarUrl: !!avatarUrl ? avatarUrl : null,
        timestamp: +Date.now(),
      };


      //   dispatch(createPost(postObject));
      var updates = {};
      updates["/usersCraft/" + userEmail + "/posts/" + idPost] = postObject;
      updates[
        "/currentDay/" + currentDay + "/posts/" + idPost
      ] = currentDayObject;
      updates["/currentDay/" + currentDay + "/cases/" + idPost] = { ...postObject, timestamp: +Date.now() };
     // updates["currentLaboratoryDay/" + currentDay + "/cases/" + idPost] = { ...postObject, timestamp: +Date.now() };

      // updates['/posts/' + user +  newPostKey] = postData;
      // updates['/user-posts/' + uid + '/' + newPostKey] = postData;
      // console.log(updates);
      return firebase.database().ref().update(updates).then(() => setSavedAnswer(true));
    }
  }

  function handleChange(event) {
    setValue(event.target.value);
  }

  return <Card
    bg={"light"}
    style={{ width: '95%', margin: '1rem' }}
  >
    <Card.Header>{props.header}</Card.Header>
    {!!props.imageurl && <Card.Img variant="top" src={props.imageurl} />}
    <Card.Body>
      <Card.Title>{props.title}</Card.Title>
      <Card.Text>
        {ReactHtmlParser(!!currentProjectComment ? currentProjectComment : "Выберите компанию (отчетность)")}
      </Card.Text>

      <Card.Text>
        {ReactHtmlParser(props.text)}
      </Card.Text>

      {props.usersTemplates.includes("calculations/") ? <EditableDataTableWithPredefinedIndicatorsForQuiz
        id={props.usersTemplates}
        title={props.title}
        theme={props.theme}
      /> : null}

      {props.usersTemplates.includes("charts/") ? <PrepareChartWithPredefinedIndicatorsForQuiz
        id={props.usersTemplates}
        title={props.title}
        theme={props.theme}
        text={props.text}
      /> : null}

      {props.usersTemplates.includes("mediaeditor/") ? <NewMediaForPostAndFundamentals
        id={props.usersTemplates}
        title={props.title}
        theme={props.theme}
        text={props.text}
      /> : null}

      {props.usersTemplates.includes("calculations/") ? <SpreadsheetLayout /> : null}
      
      {props.usersTemplates.includes("calculations") ? <InputGroup size="sm" style={{ width: "95%", margin: "1rem" }}>
        <InputGroup.Prepend>
          <Button variant="outline-secondary" onClick={handleCheckAnswer}>
            Ответ
                </Button>
        </InputGroup.Prepend>
        <FormControl
          value={value}
          type="number"
          onChange={handleChange}
          aria-label="Answer"
          aria-describedby="inputGroup-answer"
          readOnly={savedAnswer ? true : false}
        />
      </InputGroup> : null}
    </Card.Body>
  </Card>
}


export function PrepareChartWithPredefinedIndicatorsForQuiz(
  {
    id = "charts/-M_kkJgv-SB6u2pQ5uyG",
    title,
    theme,
    text
  }) {
  const { data: chart, loading, error } = useFirebaseNode(
    "usersTemplates/" + id
  );

  if (!!loading || !chart) {
    return <div>...</div>;
  }
  if (!!error) {
    return <div>:-(</div>;
  }

  console.log(chart)
  return <div>
    <PrepareReduxDataFromDictionaryForChartForQuizFilteredByCurrentProject chart={chart} title={title} theme={theme} text={text} />
    <SaveChartWithDataToPostsAndFundamentals chart={chart} title={title} theme={theme} text={text} />
  </div>

  //  return <div>PrepareChartWithPredefinedIndicatorsForQuiz</div>
}

function SaveChartWithDataToPostsAndFundamentals(props) {
  const userData = useSelector(selectUserData).data;
  const currentProjectSourseDataURL = useSelector(selectApplication)?.currentProjectSourseDataURL;
  const currentProjectMediaAndDataAndTemplatesURL = useSelector(selectApplication).currentProjectMediaAndDataAndTemplatesURL;
  const currentProjectTitle = useSelector(selectApplication).currentProjectTitle;
  const email = useSelector(selectApplication).email;
  const user = useSelector(selectApplication).user;
  const avatarUrl = useSelector(selectApplication)?.avatarUrl;
  const [savedAnswer, setSavedAnswer] = useState(null);

  function handleSaveChartPost() {
    let filteredByURLdata = userData.filter(item => item.projectSourseDataURL === currentProjectSourseDataURL);
    let requiredcategories = Object.keys(props.chart.content.requiredcategories).map(item => { return props.chart.content.requiredcategories[item] })
    let requiredseries = Object.keys(props.chart.content.requiredseries).map(item => { return props.chart.content.requiredseries[item] })
    let series = requiredseries.map(indicator => {
      return { name: indicator, data: doFindValuesForChartDataSeriesByIndicator(filteredByURLdata, indicator, requiredcategories) }
    });
    console.log(series);

    if (email.length > 6) {
      let userEmail = email.replace(/[^a-zA-Z0-9]/g, "_");
      let idPost = firebase.database().ref(userEmail).child("posts").push().key;
      let currentDay = new Intl.DateTimeFormat("en", {
        weekday: "short",
        year: "numeric",
        month: "short",
        day: "numeric",
      })
        .format(new Date())
        .replace(/[^a-zA-Z0-9]/g, "_");

      let postObject = {
        id: idPost, //Math.floor(Math.random() * 1001),
        title: props.text,
        theme: currentProjectTitle,
        //    //     answer: value,
        comment: currentProjectMediaAndDataAndTemplatesURL, //Тема
        type: props.chart.type.replace('withpredefinedindicators', ''),
        content: { series: series, categories: requiredcategories },
        quizString: 'Рассчитано по отчетности ' + currentProjectTitle + ' по адресу ' + currentProjectSourseDataURL,
        deleted: false,
        email: email,
        user: user,
        avatarUrl: !!avatarUrl ? avatarUrl : null,
        date: new Intl.DateTimeFormat("ru", {
          weekday: "short",
          year: "numeric",
          month: "short",
          day: "numeric",
          hour: "numeric",
          minute: "numeric",
        }).format(new Date()), //Date().toJSON()
      };

      console.log(postObject);

      let currentDayObject = {
        id: idPost,
        title: props.text,
        theme: props.theme,
        email: email,
        user: user,
        avatarUrl: !!avatarUrl ? avatarUrl : null,
        timestamp: +Date.now(),
      };


      //   dispatch(createPost(postObject));
      var updates = {};
      updates["/usersCraft/" + userEmail + "/posts/" + idPost] = postObject;
      updates[
        "/currentDay/" + currentDay + "/posts/" + idPost
      ] = currentDayObject;
      updates["/currentDay/" + currentDay + "/cases/" + idPost] = { ...postObject, timestamp: +Date.now() };
    //  updates["currentLaboratoryDay/" + currentDay + "/cases/" + idPost] = { ...postObject, timestamp: +Date.now() };
      return firebase.database().ref().update(updates).then(() => setSavedAnswer(true));

    }
  }

  return <Button variant="outline-secondary" size="sm" className="m-3" disabled={savedAnswer}
    onClick={() => handleSaveChartPost()}
  >
    Сохранить
      </Button>
}


