function NewProjectForm(props) {
  const email = useSelector(selectApplication).email;
  const user = useSelector(selectApplication).user;
  const avatarUrl = useSelector(selectApplication)?.avatarUrl;

  const { register, handleSubmit, watch } = useForm(); //, errors
  const [
    reportIndicatorsDictionary,
    setReportIndicatorsDictionary,
  ] = React.useState(null);
  const dispatch = useDispatch();

  function onSubmit(data) {
    //   console.log(data);

    if (email.length > 6) {
      let userEmail = email.replace(/[^a-zA-Z0-9]/g, "_");
      let idPost = firebase.database().ref(userEmail).child("posts").push().key;

      let postObject = {
        id: idPost,
        title: data?.currentProjectComment?.comment || "Комментарий",
        theme:
          data.currentProjectTitle.length > 2
            ? data.currentProjectTitle
            : "Проект",
        answer: data.currentProjectSourseDataURL,
        comment:
          data.currentProjectComment.comment > 2
            ? data.currentProjectComment.comment
            : "Комментарий",
        type: "project",
        content: reportIndicatorsDictionary,
        quizString: data.currentProjectMoneyScale,
        deleted: false,
        email: email,
        user: user,
        avatarUrl: !!avatarUrl ? avatarUrl : null,
        date: new Intl.DateTimeFormat("ru", {
          weekday: "short",
          year: "numeric",
          month: "short",
          day: "numeric",
          hour: "numeric",
          minute: "numeric",
        }).format(new Date()), //Date().toJSON()
      };

      //     console.log(postObject);
      //    dispatch(createPost(postObject));
      var updates = {};
      updates["/usersCraft/" + userEmail + "/posts/" + idPost] = postObject;
      updates["/openmedia/" + idPost] = postObject;
      updates["/usersTemplates/projects/" + idPost] = postObject;

      return firebase
        .database()
        .ref()
        .update(updates)
        .then(() => {
          //      DoSavedSuccessfully(true);
          //      doEditPostTitleComment(false);

          dispatch(
            set_currentProject({
              currentProjectTitle:
                data.currentProjectTitle.length > 2
                  ? data.currentProjectTitle
                  : "Проект",
              currentProjectComment:
                data.currentProjectComment.comment > 2
                  ? data.currentProjectComment.comment
                  : "<a href='" + data.currentProjectSourseDataURL + "' target='_blank'>" + data.currentProjectTitle + "</a>",
              currentProjectMediaAndDataAndTemplatesURL: idPost,
              currentProjectSourseDataURL: data.currentProjectSourseDataURL,
              currentProjectMoneyScale: data.currentProjectMoneyScale,
              currentProjectReportIndicatorsDictionary: reportIndicatorsDictionary,
            })
          );
          setTimeout(window.location.reload(), 3000); //; props.handleClose()
        });
    }
  }

  return (
    <form onSubmit={handleSubmit(onSubmit)}>
      <DropDownInputGroup
        items={["Project Expert", "РСБУ Приказ Минфин 66н 2010 г."]}
        title={"Стандарт отчетности"}
        buttonTitle={"Выбрать"}
        setSelectedItem={setReportIndicatorsDictionary}
      />
      {!!reportIndicatorsDictionary &&
        modalForm.map((item) => (
          <InputGroup className="mb-3" size="sm" key={item.name}>
            <InputGroup.Prepend>
              <InputGroup.Text style={{ width: "20rem" }}>
                {item.title}
              </InputGroup.Text>
            </InputGroup.Prepend>
            <FormControl
              key={item.name}
        //      name={item.name}
              type="text"
              {...register(item.name)}           
            />
          </InputGroup>
        ))}
      <Button
        variant="outline-secondary"
        type="submit"
        size="sm"
        disabled={Object.keys(watch()).length < 3}
      >
        Сохранить
      </Button>
    </form>
  );
}
