import React from 'react';
import MathJax from 'react-mathjax-preview';
import { useInView } from 'react-intersection-observer';

function ShowMathJax(props) {
  const { ref, inView } = useInView({ //, entry
    /* Optional options */
    threshold: 1,
   delay: 1000,
  triggerOnce: true
  });

  if (typeof window === `undefined`) return <div>...</div>
  return <div ref={ref}>
    {inView ? <MathJax math={String.raw`<math xmlns="http://www.w3.org/1998/Math/MathML" display="block">${props.math}</math>`} /> : <div>Формула</div>}
  </div>
}

export default ShowMathJax


