import React from "react";

const HTMLEditorClassicLazy = React.lazy(() => import("./HTMLEditorClassicLazy.js"))

export default (props) => {
    const isSSR = typeof window === "undefined"
    return <>
      {!isSSR && (
        <React.Suspense fallback={<div />}><HTMLEditorClassicLazy {...props} /></React.Suspense>
      )}
    </>
}
