import React from "react";
import { useSelector, useDispatch } from "react-redux";
import { useForm } from "react-hook-form";

import firebase from "gatsby-plugin-firebase";
import ReactHtmlParser from "react-html-parser";

//import HTMLEditorClassic from "../components/HTMLEditorClassic";
import CKEditorClassic from "../components/CKEditorClassic";

import MarkDeleteFireNodeButton from "../components/MarkDeleteFireNodeButton";


import {
    selectCKEditorData,
    setCKEditorContent,
    setCKEditorNodeId, emptyCKEditorData
} from "../features/ckeditor/ckeditorSlice";

// import {
//     selectTempData
//     , emptyCKEditorData, setCKEditorContent,setCKEditorNodeId
// } from "../features/temp/tempSlice";

import { selectApplication } from "../features/application/applicationSlice";

import Container from "react-bootstrap/Container";
import Button from "react-bootstrap/Button";
import Collapse from "react-bootstrap/Collapse";
import InputGroup from "react-bootstrap/InputGroup";
import FormControl from "react-bootstrap/FormControl";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";

import { SVGBootstrapIconPencilSquare } from "../svgs";


export default function CreateOrShowCommentPostForProject({ firenode = null }) {
    if (!firenode) { return <CreateCommentPostForProject /> }
    return <ShowOrEditCommentPostForProject post={firenode} />
}

function ShowOrEditCommentPostForProject({ post }) {
    const email = useSelector(selectApplication).email;
    let userEmail = email.replace(/[^a-zA-Z0-9]/g, "_");
    const dispatch = useDispatch();
    const [open, setOpen] = React.useState(false);
    const [editMode, setEditMode] = React.useState(false);

    function openComment() {
        //dispatch(load_data({ protoData: createProtoArray(post.content) }));
        //   dispatch(set_spreadsheetTitle({ spreadsheetTitle: post.title }));
        setOpen(!open)
    }

    function editComment() {
        dispatch(emptyCKEditorData());
        dispatch(setCKEditorContent(post.content));
        dispatch(setCKEditorNodeId(post.id));
        setEditMode(true);
    }

    // console.log(post);

    if (!!editMode) {
        return <CreateCommentPostForProject postId={post.id} title={post.title} comment={post.comment} content={post.content} />
    }

    return (
        <div className="m-2">
            <Button

                onClick={() => openComment()}
                aria-controls={"html-comment-collapse" + post.id}
                aria-expanded={open}
                variant={open ? "secondary" : "outline-secondary"}
                size="sm"
                block
            >
                <span>{post.title + " "}</span><small>{post.comment}</small>
            </Button>
            <Collapse in={open}>
                <div id={"html-comment-collapse" + post.id} className="m-2">
                    <Container>
                        <Row>
                            <Col md={4}> <Button
                                onClick={() => editComment()}
                                variant="outline-light"
                                size="sm"
                            >
                                <SVGBootstrapIconPencilSquare /> {" "} Редакт
                    </Button></Col>
                            <Col md={{ span: 4, offset: 4 }}>
                                <MarkDeleteFireNodeButton nodeRef={"usersCraft/" + userEmail + "/posts/"} id={post.id} />
                            </Col>
                        </Row>
                    </Container>



                    <div>{ReactHtmlParser(post.content)}</div>
                </div>
            </Collapse>
        </div>
    );
}

function CreateCommentPostForProject({ postId = null, title = "Заголовок", comment = "Мелкий шрифт", content = "" }) {
    const ckEditorContent = useSelector(selectCKEditorData).ckeditor_node_content;
    const email = useSelector(selectApplication).email;
    const user = useSelector(selectApplication).user;
    const avatarUrl = useSelector(selectApplication)?.avatarUrl;
    const currentProjectMediaAndDataAndTemplatesURL = useSelector(selectApplication).currentProjectMediaAndDataAndTemplatesURL;
    const currentProjectTitle = useSelector(selectApplication).currentProjectTitle;
    // const currentProjectComment = useSelector(selectApplication).currentProjectComment;
    const { register, handleSubmit, setValue } = useForm({ // reset, watch, errors
        defaultValues:
        {
            title: title,
            comment: comment
        }
    });


    function saveComment(data) {
        let userEmail = email.replace(/[^a-zA-Z0-9]/g, "_");
        let idPost = !!postId ? postId : firebase.database().ref(userEmail).child("posts").push().key;

        let postObject = {
            id: idPost,
            title: data.title,
            theme: currentProjectTitle,
            answer: currentProjectMediaAndDataAndTemplatesURL,
            comment: data.comment,
            type: "html",
            content: ckEditorContent,
            quizString: "",
            deleted: false,
            email: email,
            user: user,
            avatarUrl: !!avatarUrl ? avatarUrl : null,
            date: new Intl.DateTimeFormat("ru", {
                weekday: "short",
                year: "numeric",
                month: "short",
                day: "numeric",
                hour: "numeric",
                minute: "numeric",
            }).format(new Date()), //Date().toJSON()
        };

        console.log(postObject);

        let currentDay = new Intl.DateTimeFormat("en", {
            weekday: "short",
            year: "numeric",
            month: "short",
            day: "numeric",
        })
            .format(new Date())
            .replace(/[^a-zA-Z0-9]/g, "_");

        let currentDayObject = {
            id: idPost,
            title: data.title,
            theme: currentProjectTitle,
            email: email,
            user: user,
            avatarUrl: !!avatarUrl ? avatarUrl : null,
            timestamp: +Date.now(),
        };

        var updates = {};
        updates["/usersCraft/" + userEmail + "/posts/" + idPost] = postObject;
        updates["/openmedia/" + idPost] = postObject;

        updates[
            "/currentDay/" + currentDay + "/posts/" + idPost
        ] = currentDayObject;
        updates["/currentDay/" + currentDay + "/cases/" + idPost] = { ...postObject, timestamp: +Date.now() };

        console.log(updates);

        return firebase
            .database()
            .ref()
            .update(updates)
            .then(() => {
                //       //      DoSavedSuccessfully(true);
                //       //      doEditPostTitleComment(false);
                setTimeout(window.location.reload(), 3000); //; props.handleClose()
            });
    }

   function setHtmlfeedback() {

   } 


    return <Container>
        {/* <HTMLEditorClassic /> */}
        {!!postId ?
            <CKEditorClassic
                id={postId} content={content}
                htmlfeedback={""}
                setHtmlfeedback={setHtmlfeedback}
            />
            :
            <CKEditorClassic
                id={"project_comment"} content={""}
                htmlfeedback={""}
                setHtmlfeedback={setHtmlfeedback}
            />
        }

        <form onSubmit={handleSubmit(saveComment)}>
            <InputGroup className="mb-2 mt-3" size="sm">
                <InputGroup.Prepend>
                    <InputGroup.Text>Заголовок в списке и комментарий</InputGroup.Text>
                </InputGroup.Prepend>
                 <FormControl   {...register("title")}  /> {/* name={"title"} */}
                <FormControl   {...register("comment")}  /> {/* name={"comment"} */}
            </InputGroup>
            <Button variant="outline-secondary" type="submit" size="sm">Сохранить</Button>
        </form>

    </Container>
}