import React, { useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { useDropzone } from "react-dropzone";
import axios from "axios";
import ReactHtmlParser from 'react-html-parser';

import firebase from "gatsby-plugin-firebase";

import HTMLEditorBallonBlock from "./HTMLEditorBallonBlock";

import DatalineQuotesApexChartEditor from "../mediatemplates/DatalineQuotesApexChartEditor";

import Card from "react-bootstrap/Card";
import Alert from "react-bootstrap/Alert";

// import Modal from "react-bootstrap/Modal";
import Button from "react-bootstrap/Button";
import InputGroup from "react-bootstrap/InputGroup";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";

import { selectApplication } from "../features/application/applicationSlice";

export default function CaseWithCloudinary(props) {
  const [imageUploading, setImageUploading] = useState(false);
  const [cloudinaryURLs, setCloudinaryURLs] = useState([]);
  const [htmlfeedback, setHtmlfeedback] = useState("Комментарий");
  const [uploadError, setUploadError] = useState(null);

  const [chartSeries, setChartSeries] = useState([]);

  const onDrop = React.useCallback((acceptedFiles) => {
    setImageUploading(true);

    const uploaders = acceptedFiles.map((file) => {
      const formData = new FormData();
      formData.append("file", file);
      formData.append("tags", `2020, econolabs`);
      formData.append("upload_preset", "qzvjwavu"); // Replace the preset name with your own
      formData.append("api_key", "521328645139184"); // Replace API key with your own Cloudinary key
      formData.append("api_secret", "HDYNOu1ysDutpL7qOWovQqi1z9o");
      formData.append("timestamp", (Date.now() / 1000) | 0);
      return axios
        .post(
          "https://api.cloudinary.com/v1_1/econolabs/image/upload",
          formData,
          {
            headers: { "X-Requested-With": "XMLHttpRequest" },
          }
        )
        .then((response) => {
          setCloudinaryURLs((cloudinaryURLs) => [
            ...cloudinaryURLs,
            response.data.url,
          ]);
          //    let url = response.data.url;
        })
        .catch((err) => {
          setUploadError(err);
          //console.log(err);
        });
    });
    axios.all(uploaders).then(() => {
      setImageUploading(false);
    });

    // Do something with the files
  }, []);
  const { getRootProps, getInputProps, isDragActive } = useDropzone({ onDrop });

  return (
    <Card bg={"light"} style={{ width: "95%", margin: "1rem" }}>
      <Card.Header>{props.header}</Card.Header>
      <Card.Body>
        <Card.Title>{props.title}</Card.Title>

        <Card.Text> {ReactHtmlParser(props.text)}</Card.Text> 

        {/* <Card.Text><ReactMarkdown source={props.text} escapeHtml={false} /> </Card.Text>*/}

        <HTMLEditorBallonBlock
          htmlfeedback={htmlfeedback}
          setHtmlfeedback={setHtmlfeedback}
        />
        <hr />

        <DatalineQuotesApexChartEditor
          lines={[
            { name: 'date', label: "Дата", type: "date" },
            { name: 'chart1', label: "Факт", type: "number" },
            { name: 'chart2', label: "Стратегия 1", type: "number" },
            { name: 'chart3', label: "Стратегия 2", type: "number" },
            { name: 'chart4', label: "Стоп-лосс", type: "number" },
          ]}
          setChartSeries={setChartSeries}
        />

        <hr />

        <Card.Text>
          Требуется представить картинок: {props.numberofpictures}
        </Card.Text>

        <Alert
          variant="secondary"
          className="text-center mt-5"
          {...getRootProps()}
        >
          <input {...getInputProps()} />
          {isDragActive ? "Перенесите сюда ..." : "Выберите файлы (картинки)"}
        </Alert>

        {!!uploadError ? <Alert variant="danger">{uploadError}</Alert> : null}
        {!!cloudinaryURLs && !!cloudinaryURLs.length > 0 && !imageUploading ? (
          <Row>
            {cloudinaryURLs.map((url) => (
              <Col key={url}>
                <img src={url} className="img-fluid" alt="trading" />
              </Col>
            ))}
          </Row>
        ) : null}
      </Card.Body>
      {!!cloudinaryURLs &&
        cloudinaryURLs.length === props.numberofpictures &&
        !!htmlfeedback && (
          <SubmitAnswer
            cloudinaryURLs={cloudinaryURLs}
            htmlfeedback={htmlfeedback}
            chartSeries={chartSeries}
            {...props}
          />
        )}
    </Card>
  );
}

function SubmitAnswer(props) {
  const [answerIsRight, setAnswerIsRight] = useState(null);
  const dispatch = useDispatch();
  const email = useSelector(selectApplication).email;
  const user = useSelector(selectApplication).user;
  const avatarUrl = useSelector(selectApplication)?.avatarUrl;

  //  function handleChange(event) { setValue(event.target.value); }

  function handleCheckAnswer() {
    let res = true;
    if (!!res) {
      setAnswerIsRight(true);

      let userEmail = email.replace(/[^a-zA-Z0-9]/g, "_");
      let idData = firebase.database().ref(userEmail).child("data").push().key;
      let idPost = firebase.database().ref(userEmail).child("posts").push().key;

      let postObject = {
        id: idPost,
        idPost,
        title: props.title,
        theme: props.theme,
        answer: props.htmlfeedback,
        comment: props.title + " (" + props.theme + ")", //Тема
        type: "media",
        content: idData,
        quizString: props.text,
        htmlfeedback: props.htmlfeedback,
        deleted: false,
        email: email,
        user: user,
        avatarUrl: !!avatarUrl ? avatarUrl : "",
        date: new Intl.DateTimeFormat("ru", {
          weekday: "short",
          year: "numeric",
          month: "short",
          day: "numeric",
          hour: "numeric",
          minute: "numeric",
        }).format(new Date()), //Date().toJSON()
      };

      let currentDay = new Intl.DateTimeFormat("en", {
        weekday: "short",
        year: "numeric",
        month: "short",
        day: "numeric",
      })
        .format(new Date())
        .replace(/[^a-zA-Z0-9]/g, "_");

      let currentDayObject = {
        id: idPost,
        title: props.title,
        theme: props.theme,
        email: email,
        user: user,
        avatarUrl: !!avatarUrl ? avatarUrl : null,
        timestamp: +Date.now(),
      };

      let dataObject = {
        images: {
          urls: props.cloudinaryURLs,
          user: user,
          avatarUrl: avatarUrl,
        },
        datalinequotesapexchart: {
          series: props.chartSeries,
          options: { stroke: { curve: "smooth" } },
          user: user,
          avatarUrl: avatarUrl,
        },
      };

      console.log(dataObject);

      var updates = {};
      updates[
        "/currentDay/" + currentDay + "/posts/" + idPost
      ] = currentDayObject;

      updates["/usersCraft/" + userEmail + "/posts/" + idPost] = postObject;
      updates["/usersCraft/" + userEmail + "/data/" + idData] = dataObject;

      updates["/openmedia/" + idPost] = postObject;
      updates["/openmediadata/" + idData] = dataObject;
      return firebase.database().ref().update(updates);
    } else {
      console.log(res);
    }
  }

  return (
    <>
      <InputGroup size="sm" style={{ width: "95%", margin: "1rem" }}>
        <InputGroup.Prepend>
          <Button variant="outline-secondary" onClick={handleCheckAnswer}>
            Отправить ответ
          </Button>
        </InputGroup.Prepend>
      </InputGroup>
      {answerIsRight ? <Alert variant={"success"}>Правильно</Alert> : null}
    </>
  );
}
