import React from "react";
import createNewDraft from "../features/spreadsheet/createNewDraft";

import Table from "react-bootstrap/Table";

function calculatePortfolioMonthlyReturns(assetA, assetB) {
    return assetA.map((monthlyReturn, index) => { return Math.round((assetA[index] + assetB[index]) * 1000 / 2)/1000  })
}

function stockRandomReturns(number) {
    return [...new Array(number)].map(() => {
        return Math.round(Math.random() * 10000 - 4000) / 1000
    });
}

export default function PortfolioPlayground() {

    let assetAMonthlyReturns = stockRandomReturns(30);
    let assetBMonthlyReturns = stockRandomReturns(30);
    let portfolioMonthlyReturns = calculatePortfolioMonthlyReturns(assetAMonthlyReturns,  assetBMonthlyReturns);

    let assetAStatistics = [assetAMonthlyReturns, ['=AVERAGE(A1:Z1)', '=STDEVA(A1:Z1)']];
    let resultA = createNewDraft(assetAStatistics);

    let assetBStatistics = [assetBMonthlyReturns, ['=AVERAGE(A1:Z1)', '=STDEVA(A1:Z1)']];
    let resultB = createNewDraft(assetBStatistics);

    let portfolioStatistics = [portfolioMonthlyReturns, ['=AVERAGE(A1:Z1)', '=STDEVA(A1:Z1)']];
    let resultPortfolio = createNewDraft(portfolioStatistics);


    return <div>
        <Table striped bordered hover size="sm">
            <thead>
                <tr>
                    <th>{' '}Месяц</th>
                    <th>Акция А</th>
                    <th>Акция B</th>
                    <th>Портфель 50%+50%</th>
                </tr>
            </thead>
            <tbody>
                {assetAMonthlyReturns.map((monthReturn, index) =>
                    <tr>
                        <td>{' '}{index}</td>
                        <td>{assetAMonthlyReturns[index]}</td>
                        <td>{assetBMonthlyReturns[index]}</td>
                        <td>{portfolioMonthlyReturns[index]}</td>
                    </tr>
                )}
                <tr>
                    <td>{' Среднее'}</td>
                    <td>{resultA[1][0]}</td>
                    <td>{resultB[1][0]}</td>
                    <td>{resultPortfolio[1][0]}</td>
                </tr>
                <tr>
                    <td>{' Сигма'}</td>
                    <td>{resultA[1][1]}</td>
                    <td>{resultB[1][1]}</td>
                    <td>{resultPortfolio[1][1]}</td>
                </tr>
            </tbody>
        </Table>
    </div>
}