import React, { useState } from "react";

import ReactHtmlParser from 'react-html-parser';

import Card from 'react-bootstrap/Card'
import Modal from 'react-bootstrap/Modal'
import Button from 'react-bootstrap/Button'
import InputGroup from 'react-bootstrap/InputGroup'
import FormControl from 'react-bootstrap/FormControl'


import { Parser as FormulaParser } from "hot-formula-parser";

export default function QuizCardChildren(props) {
  const [show, setShow] = useState(false);

  let randomNumber = Math.random() * 10;

  if (props.text.includes('{=')) {
    return <QuizWithRandomNumber {...props} randomNumber={randomNumber} />
  }

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  return <>
    <Card
      bg={"light"}
      style={{ width: '95%', margin: '1rem' }}
    >
      <Card.Header onClick={handleShow}>{props.header}</Card.Header>
     {!!props.imageurl && <Card.Img variant="top" src={props.imageurl} />} 
      <Card.Body>
        <Card.Title>{props.title}</Card.Title>
        <Card.Text>
          {ReactHtmlParser(props.text)}

        </Card.Text>
        {props.children}
      </Card.Body>
    </Card>
    <Modal show={show} onHide={handleClose} size='xl' scrollable centered>
      <Modal.Header closeButton>
        <Modal.Title>{props.title}</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        {ReactHtmlParser(props.text)}
        {props.children}
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={handleClose}>
          Закрыть
    </Button>
      </Modal.Footer>
    </Modal>
  </>
}


function QuizWithRandomNumber(props) {
  const [show, setShow] = useState(false);
  const [value, setValue] = useState('');
  const [showAnswer, setShowAnswer] = useState(null);
  const [answerIsRight, setAnswerIsRight] = useState(null);


  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  function handleChange(event) {
    setValue(event.target.value);
  }



  let parser = new FormulaParser();

  function extract([beg, end]) {
    const matcher = new RegExp(`${beg}(.*?)${end}`, 'gm');
    const normalise = (str) => str.slice(beg.length, end.length * -1);
    return function (str) {
      return str.match(matcher).map(normalise);
    }
  }

  let quizString = props.text;
  //let quizString = `this {={var1-10}+1} some {=2+{var1-10}} that can be {=3+{var1-10}} with a {=4+{var1-10}} function`;
  const searchRegExp = /{var1-10}/g;
  const replaceWith = props.randomNumber.toString();
  quizString = quizString.replace(searchRegExp, replaceWith);

  let answer = props.answer.replace(searchRegExp, replaceWith);
  answer = Math.round(parser.parse(answer).result * 1000) / 1000;

  let stringExtractor = extract(['{=', '}']);
  let stuffIneed = stringExtractor(quizString);
  //console.log(stuffIneed);
  // Outputs: [ 'is', 'text', 'extracted', 'reusable' ]

  for (let i = 0; i < stuffIneed.length; i++) {
    let feedback = Math.round(parser.parse(stuffIneed[i]).result * 1000) / 1000;
    // console.log(answer);
    quizString = quizString.replace('{=' + stuffIneed[i] + '}', feedback);
  }

  function handleCheckAnswer() {
    setShowAnswer(true);
    if ((parseFloat(value) / parseFloat(answer) < 1.02) && (parseFloat(value) / parseFloat(answer) > 0.98)) { setAnswerIsRight(true) }
  }

  return <>
    <Card
      bg={"light"}
      style={{ width: '95%', margin: '1rem' }}
    >
      <Card.Header onClick={handleShow}>{props.header}</Card.Header>
      <Card.Body>
        <Card.Title>{props.title}</Card.Title>
        <Card.Text>
          {ReactHtmlParser(quizString)}

        </Card.Text>
      </Card.Body>
      <InputGroup size="sm" style={{ width: '95%', margin: '1rem' }}>
        <InputGroup.Prepend>
          {showAnswer ? <InputGroup.Text id="basic-addon1">
            <span className={answerIsRight ? "text-success" : "text-danger"} >Правильный ответ: {answer}</span>
          </InputGroup.Text>
            : <Button variant="outline-secondary" onClick={handleCheckAnswer}>Ответ</Button>}

        </InputGroup.Prepend>
        <FormControl value={value} type="number" onChange={handleChange} aria-label="Answer" aria-describedby="inputGroup-answer" />
      </InputGroup>
    </Card>
    <Modal show={show} onHide={handleClose} size='xl' scrollable centered>
      <Modal.Header closeButton>
        <Modal.Title>{props.title}</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        {ReactHtmlParser(quizString)}
      </Modal.Body>
      <Modal.Footer>
        <InputGroup size="sm" style={{ width: '95%', margin: '1rem' }}>
          <InputGroup.Prepend>
            {showAnswer ? <InputGroup.Text id="basic-addon1">
              <span className={answerIsRight ? "text-success" : "text-danger"} >Правильный ответ: {answer}</span>
            </InputGroup.Text>
              : <Button variant="outline-secondary" onClick={handleCheckAnswer}>Ответ</Button>}
          </InputGroup.Prepend>
          <FormControl value={value} type="number" onChange={handleChange} aria-label="Answer" aria-describedby="inputGroup-answer" />
        </InputGroup>
      </Modal.Footer>
    </Modal>
  </>
}