import Collapse from "react-bootstrap/Collapse";
import InputGroup from "react-bootstrap/InputGroup";
import FormControl from "react-bootstrap/FormControl";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";

import { SVGBootstrapIconPencilSquare } from "../svgs";



export default function CreateOrShowScatterApexChartPostForProject({ firenode = null }) {
    if (!firenode) { return <CreateScatterApexChartPostForProject /> }
    return <ShowOrEditScatterApexChartPostForProject post={firenode} />
}


function ShowOrEditScatterApexChartPostForProject({ post }) {
    const email = useSelector(selectApplication).email;
    let userEmail = email.replace(/[^a-zA-Z0-9]/g, "_");
    // const dispatch = useDispatch();
    const [open, setOpen] = React.useState(false);
    const [editMode, setEditMode] = React.useState(false);

    function editChart() {
        setEditMode(true);
    }

    function openScatterApexChart() {
        //   dispatch(load_data({ protoData: createProtoArray(post.content) }));
        //   dispatch(set_spreadsheetTitle({ spreadsheetTitle: post.title }));
        setOpen(!open)
    }

    // console.log(post);

    if (!!editMode) {
        return <CreateScatterApexChartPostForProject postId={post.id} title={post.title} comment={post.comment} editSeries={post.content.series} />
    }

    return (
        <div className="m-2">
            <Button

                onClick={() => openScatterApexChart()}
                aria-controls={"Scatter-ApexChart-collapse" + post.id}
                aria-expanded={open}
                variant={open ? "secondary" : "outline-secondary"}
                size="sm"
                block
            >
                 <span>{post.title+" "}</span><small>{post.comment}</small> 
            </Button>
            <Collapse in={open}>
                <div id={"Scatter-ApexChart-collapse" + post.id} className="m-2">

                    <Container>
                        <Row>
                            <Col md={4}> <Button
                                onClick={() => editChart()}
                                variant="outline-light"
                                size="sm"
                            >
                            <SVGBootstrapIconPencilSquare /> {" "} Редакт
                    </Button></Col>
                            <Col md={{ span: 4, offset: 4 }}>
                                <MarkDeleteFireNodeButton nodeRef={"usersCraft/" + userEmail + "/posts/"} id={post.id} />
                            </Col>
                        </Row>
                    </Container>

                    <GetReadyScatterApexChart firenode={post.content} />
                </div>
            </Collapse>
        </div>
    );
}


function doCreateProtoArrayFromChartSeries(series) {
    let newProtoArray = [];
    for (const element of series[0].data) {
      newProtoArray.push([element[0], element[1]]);
    }
    return newProtoArray;
  }
  
